import { ArchivoTexto } from '../../vue/utils.js';
/**
 * Regresa el ArchivoTexto package.json
 * contiene las dependecias del proyeto.
 * @param {string} nombre nombre de la aplicación
 */
export function getPakageJson(nombre) {
    return new ArchivoTexto('package.json',`
{
    "name": "${nombre.trim().replace(/ /g,'-')}",
    "version": "1.0.0",
    "scripts": {
        "ng": "ng",
        "start": "ng serve",
        "build": "ng build",
        "test": "ng test",
        "lint": "ng lint",
        "e2e": "ng e2e"
    },
    "private": true,
    "dependencies": {
        "@angular/animations": "~10.1.3",
        "@angular/common": "~10.1.3",
        "@angular/compiler": "~10.1.3",
        "@angular/core": "~10.1.3",
        "@angular/forms": "~10.1.3",
        "@angular/platform-browser": "~10.1.3",
        "@angular/platform-browser-dynamic": "~10.1.3",
        "@angular/router": "~10.1.3",
        "rxjs": "~6.6.0",
        "tslib": "^2.0.0",
        "zone.js": "~0.10.2"
    },
    "devDependencies": {
        "@angular-devkit/build-angular": "~0.1001.3",
        "@angular/cli": "~10.1.3",
        "@angular/compiler-cli": "~10.1.3",
        "@types/node": "^12.11.1",
        "@types/jasmine": "~3.5.0",
        "@types/jasminewd2": "~2.0.3",
        "codelyzer": "^6.0.0",
        "jasmine-core": "~3.6.0",
        "jasmine-spec-reporter": "~5.0.0",
        "karma": "~5.0.0",
        "karma-chrome-launcher": "~3.1.0",
        "karma-coverage-istanbul-reporter": "~3.0.2",
        "karma-jasmine": "~4.0.0",
        "karma-jasmine-html-reporter": "^1.5.0",
        "protractor": "~7.0.0",
        "ts-node": "~8.3.0",
        "tslint": "~6.1.0",
        "typescript": "~4.0.2"
    }
}
    `);
}