import { BaseBootstrap } from "../../base/componente-bootstrap.js";
const template = /*html*/`
<div class="row titulo">
    <div class="col-sm-2 col-md-2 espacio">        
    </div>
    <div class="col-sm-8 col-md-8">
        <h1 propiedades contenteditable="true">Título</h1>
    </div>
    <div class="col-sm-2 col-md-2" no-usar>
        <select class="w3-select w3-border">
            <option value="H1">H1</option>
            <option value="H2">H2</option>
            <option value="H3">H3</option>
            <option value="H4">H4</option>
            <option value="H5">H5</option>
            <option value="H6">H6</option>
        </select>
    </div>
</div>

`;
const style = '';
class TituloBS extends BaseBootstrap {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();              
    }

    addListeners() {
        let select = this.shadowRoot.querySelector('select');
        select.addEventListener('change', ev => {
            this.changeTitulo(select.value);
        });
    }

    changeTitulo(h) {
        let contenedor = this.shadowRoot.querySelector('.col-sm-8');
        let div = document.createElement(h);
        div.setAttribute('propiedades', '');
        div.setAttribute('contenteditable', 'true');
        let titulo = contenedor.firstElementChild;
        div.innerHTML = titulo.innerHTML;
        contenedor.append(div);
        titulo.remove();
        this.addListenersPropiedades();
    }

    addAllListenersShadowRoot() {
        super.addAllListenersShadowRoot();
        this.addListeners();
        let contenedor = this.shadowRoot.querySelector('.col-sm-8');
        let select = this.shadowRoot.querySelector('select');
        let titulo = contenedor.firstElementChild;
        select.value = titulo.tagName;
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.titulo');
        const div = original.cloneNode(true);
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.titulo')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-bs-titulo", TituloBS);