import { config } from "../../../../config.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding comentario w3-margin-top">
<div class="w3-col s12">
    <blockquote class="w3-leftbar w3-pale-green w3-border-red w3-padding" contenteditable="true" propiedades>
        <p><i class="fa fa-quote-right w3-xxlarge"></i></p>
        <p>Make it as simple as possible, but not simpler.</p>
        <p style="font-weight: bold;" class="w3-small w3-bold">Albert Einstein</p>
    </blockquote>
</div>
</div>

`;
const style = '';
class TextoComentario extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.host}/fonts/fafafont/font-awesome.min.css">
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.comentario')    
        const div = original.cloneNode(true);  
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.comentario')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-comentario", TextoComentario);