import { Modal } from "../../utils/modal/modal.js";

export function validaProyecto(div) {
    div.idProyecto = parseInt(div.getAttribute('idproyecto'));
    if (!div.idProyecto) {
        let dlg = new Modal();
        dlg.titulo = '<i class="fa fa-warning"></i> Alerta';
        dlg.innerHtml = 'No puede abrir así esta app';
        div.parentNode.cierraVentana();
        dlg.open().then(e => {}).catch(e => {});
    }
}