import  { config } from "../../config.js";
import { getDB } from "../../utils/db/db.js";
import { addNotificacion, NOTIFICACION } from "../../utils/notificacion/notificaciones.js";
import { addWindowConParametros } from "../../utils/ventana/agregarApps.js";
import { validaProyecto } from "../proyecto/utils-proyecto.js";
import { dataCss, dataJs } from "./codigos-iniciales.js";

const style = /*css*/`
#buscar {
    transition: width 0.4s ease-in-out;
    font-size: small;
    width: 100px;    
}
#buscar:focus {
    width: 300px;
}
`;
export class PaginadorVistas extends HTMLElement {
    constructor() {
        super();
        this.db = getDB();  
        this.lista = [];
        this.idProyecto = null;
        this.tabla = 'vistas';
        this.titulo = 'Vistas';
        this.color = 'teal';
        this.css = 'w3css';
        this.vistaPrevia = 'x-editor-basico-vista';
        this.icono = 'table';
    }
    
    connectedCallback() {
        this.idProyecto = parseInt(this.getAttribute('idproyecto'));
        validaProyecto(this);
        this.render();        
        this.cargaLista();
        this.cargaCssProyecto();
    }

    cargaCssProyecto() {
        this.db.get('proyectos', this.idProyecto).then(data => {
            this.css = data.configuracion.css;
        });
    }

    cargaLista() {
        this.db.list(this.tabla).then(data => {
            this.lista = [];
            data.forEach(item => {
                item.idProyecto == this.idProyecto ? 
                    this.lista.push(item) : '';
            });
            this.paginador.setLista(this.lista);
        });
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <link rel="stylesheet" href="${config.fontAwesome}">
        <style>${style}</style>     
        <x-bar-menu color="w3-${this.color}" buscar></x-bar-menu>        
        <x-paginador-files titulo="${this.titulo}" vista-previa="${this.vistaPrevia}"></x-paginador-files>

        `;
        this.menu = shadowRoot.querySelector('x-bar-menu');
        this.paginador = shadowRoot.querySelector('x-paginador-files');
        this.paginador.setLista(this.lista);
        this.addListeners();        
        this.menu.setItem({
            titulo: this.titulo,            
            icono: this.icono,
            acciones: [{
                titulo: 'Nuevo',
                icono: 'fa-file-o',
            }]
        });
    }

    addListeners() {
        this.paginador.addEventListener('onVistaPrevia', (e) => {
            const r = e.detail;
            this.onVistaPrevia(r.vistaPrevia, r.item);
        });
        this.paginador.addEventListener('onOpenItem', (e) => {
            const r = e.detail;            
            this.onOpenItem(r.item);
        });
        const padre = this.parentNode;
        padre.addEventListener('nuevo', (ev) => {
            this.addItem();
        });        
        this.menu.addEventListener('accion', ev => {
            let data = ev.detail;
            if (data.accion.titulo == 'Nuevo') {
                this.addItem();
            }
        });
        this.menu.addEventListener('buscar', ev => {
            let data = ev.detail;
            this.buscaElementos(data.value);
        });
    }
    /**
     * Metodo que se ejecuta cuando se da clic algun item del paginador
     * @param {HTMLElement} vistaPrevia Es la instancia de la vista previa creada
     * @param {Object} item Es un objecto json
     */
    onVistaPrevia(vistaPrevia, item) {
        vistaPrevia.setItem(item);
        vistaPrevia.addEventListener('actualizar', ev => {
            const re = ev.detail;
            this.actualizaLista(re.id, re.nombre);
        });
        vistaPrevia.addEventListener('eliminar', ev => {
            const re = ev.detail;
            this.eliminaItem(re.id);
        });
    }

    onOpenItem(item) {
        this.db.get('metadataVistas', item.id).then(data => {
            let app = addWindowConParametros('x-ide', item.nombre, null, true, 
                {
                    tipo: this.tabla,
                    css: this.css
                });
            app.setVista(data);
            app.addEventListener('guardarVista', (ev) => {
                this.actualizaMetadataVista(item.nombre, item.id, ev.detail.item);
            });
        });        
    }

    actualizaMetadataVista(nombre, id, data){
        data.id = id;     
        this.db.put('metadataVistas', data).then((r)=>{
            addNotificacion(NOTIFICACION.SUCCESS, 
                '<i class="fa fa-check-square-o"></i> Guardado', 
                'Ha editado corretamente la vista '+nombre,
                5000);
        });
    }

    buscaElementos(nombre) {        
        let buscar = [];
        nombre = nombre.trim();
        if (nombre == '') {
            this.paginador.setLista(this.lista);
            return;
        }
        nombre = nombre.toUpperCase();
        for(let item of this.lista){
            if (item.nombre.toUpperCase().indexOf(nombre) >= 0){
                buscar.push(item);
            }
        }
        this.paginador.setLista(buscar);
    }

    buscaIndexItem(id) {
        return this.lista.findIndex(item => {
            return item.id == id;
        });
    }
    
    eliminaItem(id) {
        const i = this.buscaIndexItem(id);
        if (i<0) return;
        console.log(id);
        this.db.delete(this.tabla, id).then(data => {
            this.lista.splice(i,1);
            this.paginador.setLista(this.lista);
            this.resetBuscador();        
        });
    }

    resetBuscador() {
        let buscar = this.shadowRoot.querySelector('#buscar');
        buscar.value = '';
    }

    actualizaLista(id, nombre){        
        const i = this.buscaIndexItem(id);
        if (i<0) return;
        this.lista[i].nombre = nombre;
        this.db.put(this.tabla, this.lista[i]).then(data => {                        
            this.paginador.setLista(this.lista);
            this.paginador.setLista(this.lista);
            this.resetBuscador();
        });
    }

    addItem() {
        let item = {
            nombre: 'Vista',
            idProyecto: this.idProyecto
        };
        this.db.post(this.tabla, item).then(data => {            
            this.lista.push(data);
            this.paginador.setLista(this.lista);
            this.db.post('metadataVistas', {
                id: data.id,
                codigo: dataJs,
                estilo: dataCss,
                componentes: []
            }, false);
        });        
    }
}
customElements.define('x-paginador-vistas', PaginadorVistas);