import { toCammelCase } from "../../../exports/nombres.js";
import { getDB } from "../../../utils/db/db.js";
import { jsonToListaObjectos } from "../../editor-caminos/itemsCanvas.js";
import { ArchivoTexto, creaArbol, getCamino, getNombreArchivo } from "./utils.js";

/**
 * Regresa el ArchivoTexto que contiene el main.js
 * es generico y puede ser usado sin importar el css
 */
export function getMain(){
    return new ArchivoTexto('src/main.js',
    `
import Vue from 'vue';
import VueRouter from 'vue-router';
import {getRutas} from './rutas';
import App from './app';
Vue.use(VueRouter);

const router = getRutas();

new Vue({
    router: router,
    el: '#app',
    render: (context) => context(App)
});
    `);
}
/**
 * Regresa el ArchivoTexto que contiene ala app.vue
 * Este es generico y puede ser usado sin importar el
 * estilo css usado
 */
export function getApp() {
    return new ArchivoTexto('src/app.vue', `
<template>
    <div>
        <router-view></router-view>
    </div>
</template>

<script>
export default {
}
</script>
    `);
}
/**
 * Genera una arreglo de ArchivsTexto con todas las rutas
 */
export async function generarRutas(idProyecto) {
    let archivos = [];
    let db = getDB();
    let grupos = await db.list('grupos');
    grupos = grupos.filter(grupo => {return grupo.idProyecto == idProyecto});
    let caminos = await db.list('caminos');
    for(let i=0; i<grupos.length; i++){
        let camino = getCamino(grupos[i].id, caminos);
        const flujo = jsonToListaObjectos(camino.flujo);        
        let nombre = grupos[i].nombre
            .trim()
            .toLowerCase()
            .replace(/ /g,'-');        
        let rutaInicial= '/' + flujo[0].contexto.trim()
            .toLowerCase()
            .replace(/ /g,'-');        
        let rutas = [{ruta: rutaInicial, nodo: flujo[0]}]
            .concat(creaArbol(rutaInicial, flujo[0].caminoInicial));
        const modulorutas = await creaModuloRutas(nombre, rutas);        
        archivos = archivos.concat(modulorutas);
    }    
    archivos.push(creaArchivosRutas(grupos));    
    return archivos;
}
/**
 * Regresa un Archivo de texto con las rutas del proyecto
 * @param {[]} grupos Lista de grupos, obtenida directamente del la bd
 */
function creaArchivosRutas(grupos) {
    let imports = '';
    let cmd = '';
    for(let grupo of grupos) {
        let nombre = grupo.nombre.trim().toLowerCase();        
        imports += `import { getRutas${toCammelCase(nombre)} } from './flujos/${nombre}/rutas${toCammelCase(nombre)}.js'\n`;
        cmd += `routes = routes.concat(getRutas${toCammelCase(nombre)}());\n`;
    }

    return new ArchivoTexto('src/rutas.js', `
import VueRouter from 'vue-router';
${imports}
let routes = [];
${cmd}

export function getRutas(){ 
    return new VueRouter({
        routes 
    })
}
    `);
}
function getNombreVista(ruta) {
    return ruta.nodo.nombre
        .toLowerCase()
        .replace(/ /g, '-');
}

async function creaModuloRutas(nombre, rutas) {
    let db = getDB();
    let imports = new Set();
    let importstext = '';
    let paths = '';
    let archivos = [];
    let idHeader = parseInt(rutas[0].nodo.header);
    let idFooter = parseInt(rutas[0].nodo.footer);
    let header = null;
    let footer = null;
    if (idHeader) {
        header = await db.get('headers', idHeader);
    }
    if (idFooter) {
        footer = await db.get('footers', idFooter);
    }    
    let nombreHeader = header ? getNombreArchivo(header.nombre) : null;
    let nombreHFooter = footer ? getNombreArchivo(footer.nombre) : null;
    for (let ruta of rutas) {
        const vistaCamino = await creaVistaCamino(nombre, ruta, nombreHeader, nombreHFooter);
        archivos.push(vistaCamino);
        let nombreComponente = toCammelCase( ruta.nodo.nombre.trim().replace(/ /g, '-').toLowerCase());
        paths += `        { path: '${ruta.ruta}', component: ${nombreComponente} } ,\n`;
        let imp = `import ${nombreComponente} from './${getNombreVista(ruta)}';`;
        if (!imports.has(imp)) {
            imports.add(imp);
            importstext += imp+'\n';
        }
    }
    let archivoRutas =  new ArchivoTexto(`src/flujos/${nombre}/rutas${toCammelCase(nombre)}.js`, `
${importstext}
export function getRutas${toCammelCase(nombre)}(){
    return [
${paths}
    ];
}
    `);
    archivos.push(archivoRutas);
    return archivos;
}

async function creaVistaCamino(nombre, ruta, header, footer) {
    let db = getDB();
    let idVista = parseInt(ruta.nodo.vista);
    let vista = null;
    if (idVista) {
        vista = await db.get('vistas', idVista);        
    }
    const nombreArchivo = `src/flujos/${nombre}/${getNombreVista(ruta)}.vue`;
    const nombreContent = vista ? getNombreArchivo(vista.nombre) : '';
    const importcontent = vista ? `import ContentDiv from '../../vistas/${nombreContent}.vue';` : '';
    const tagContent = vista ? '<content-div></content-div>' : '';
    const defContent = vista ? 'ContentDiv' : '';
    const tagHeader = header ? '<header-div></header-div>' : '';
    const tagFooter = footer ? '<footer-div></footer-div>' : '';
    const importHeader = header ? `import HeaderDiv from '../../headers/${header}.vue';` : '';
    const importFooter = footer ? `import FooterDiv from '../../footers/${footer}.vue';` : '';
    const defHeader = header ? `HeaderDiv,` : '';
    const defFooter = footer ? `FooterDiv,` : '';
    return new ArchivoTexto(nombreArchivo, `
<template>
    <div>
    ${tagHeader}
    ${tagContent}
    ${tagFooter}
    </div>    
</template>
<script>
    ${importHeader}
    ${importcontent}
    ${importFooter}
    export default {
        data() {
            return {
                mensaje: 'Hola mundo',
            }
        },
        components: {
            ${defFooter}
            ${defHeader}
            ${defContent}
        }
    }
</script>

    `);
}