class ServiciosWcss extends BaseW3CSS {
    constructor(){
        super();        
        this.render();
        this.color = 'teal';
    }
  
    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();          
    }

    setColor(color) {        
        let divs = this.shadowRoot.querySelectorAll(`.w3-${this.color}, .w3-text-${this.color}`);
        for (let div of divs) {
            div.classList.value = div.classList.value
                .replace(`w3-${this.color}`, `w3-${color}`)
                .replace(`w3-text-${this.color}`, `w3-text-${color}`);
        }
        this.color = color;
    }

    addListenerImagenes() {
        let imgs = this.shadowRoot.querySelectorAll('img');
        for( let img of imgs) {
            img.addEventListener('click', ev => this.onChangeImg(img));
        }
    }

    addListenerAClon(clon) {
        super.addListenerAClon(clon);
        const img = clon.querySelector('img');
        img.addEventListener('click', ev => this.onChangeImg(img));
    }

    onChangeImg(img){        
        let dlg = new ModalGet('Dame url de imagen', img.src);
        dlg.open().then(dlg => {
            img.src = dlg.data;
        });
    }

    addListeners(){        
        this.addListenerImagenes();    
    }
    

    getHtml() {        
        const original = this.shadowRoot.querySelector('[template-servicios]')    
        const div = original.cloneNode(true); 
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('[template-servicios]')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    addAllListenersShadowRoot() {
        super.addAllListenersShadowRoot();
        this.addListeners();        
    }
}

customElements.define("x-template-servicios", ServiciosWcss);