import { getBackgroundColor } from "../../appVue/ide/utils.js";
import { config } from "../config.js";
import { toCammelCase } from "./nombres.js";

function getListaProps(obj){
    let salida = '';
    for(let p in obj.props) {
        salida += `'${p}',`;
    }
    return salida;
}
function getValorVariable(valor) {
    if (typeof valor == 'string') {
        return `'${valor}'`;
    }    
    if (typeof valor == 'object') {
        return JSON.stringify(valor);
    }
    return valor;
}

function getVariables(customScript) {
    let variables = '';
    const data = customScript.data;
    for (let key in data) {
        variables += `        this.${key} = ${getValorVariable(data[key])};\n`;
    }
    return variables;
}

function getFunciones(obj){
    let fun = '';
    const data = obj.metodos;
    for (let f in data) {
        fun += `    ${data[f].toString()}\n`;
    }
    fun += '    ' + obj.init.toString()+'\n';
    return fun;
}

export function tagToClassName(tag) {
    return toCammelCase(tag);
}

export function getCss(tipo) {    
    if (tipo == 'sketchy') {
        return config.bootstrap.sketchy;
    }
    if (tipo == 'materia') {
        return config.bootstrap.materia;
    }
    if (tipo == 'literia') {
        return config.bootstrap.literia;
    }
    if (tipo == 'superhero') {
        return config.bootstrap.superhero;
    }
    if (tipo == 'minty') {
        return config.bootstrap.minty;
    } 
    return config.bootstrap.default;
}

export function getCsslink(tipo) {    
    if (tipo == 'w3css' || tipo == 'null') {
        return config.w3css;
    }
    return getCss(tipo);
}

function getFontDefault(tipo) {
    if (tipo == 'sketchy') {
        return `this.style.fontFamily = "'Neucha', cursive"; `;
    }
    return '';
}
export function getWebcomponent(tag, componentes, customStyle, obj, tipocss) {
    const nombreClase = tagToClassName(tag);
    let estilo = new Set();
    let style = '';
    let template = '';
    let metodos = '';
    let init = '';
    let css = getCsslink(tipocss);
    let font = getFontDefault(tipocss);
    let color = getBackgroundColor(tipocss);
    for(let item of componentes) {
        if (!estilo.has(item.tagName)){
            estilo.add(item.tagName);
            style += item.getStyle();
        }
        template += item.getWebComponenteTemplate();
        metodos += item.getWebComponenteMetodos();
        init += item.getWebComponenteInit();
    }
    template 
    return `
const style = /*css*/\`
* {
    background: ${color};
}
.espacio{
    min-height: 10px;
}
${customStyle}
${style}
\`;

const template = /*html*/\`
${template}
\`;

class ${nombreClase} extends HTMLElement {
    constructor(){
        super();
${getVariables(obj)}
        this.render();   
        this.initComponentes();
        this.init();
    }

    initComponentes() {
        ${init}
    }

    ${getFunciones(obj)}

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        let customTemplate = this.replazaValoresMustache(template);
        shadowRoot.innerHTML = \`
            <link rel="stylesheet" href="${css}">
            <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
            <style>\${style}</style>
            <div>\${customTemplate}</div>
        \`;
        this.$el = shadowRoot.querySelector('div');
        ${font}
    }
    /*Cuando se monta en la pagina*/
    connectedCallback(){        
    }
    /*Cuando se desmonta en la pagina, no necesariamente se destruye*/
    disconnectedCallback(){        
    }

    replazaValoresMustache(template){
        let props = [${getListaProps(obj)}];
        let salida = template + '';
        for (let p of props){
            const exp = new RegExp(\`{{\${p}}}\`, 'g');
            const attr = this.getAttribute(p);
            salida = salida.replace(exp, attr);
        }
        return salida;
    }

    ${metodos}

}

customElements.define("${tag}", ${nombreClase});
    `;
}