import { ArchivoTexto } from '../../vue/utils.js';

const tslinFile = `{
    "extends": "tslint:recommended",
    "rulesDirectory": [
      "codelyzer"
    ],
    "rules": {
      "align": {
        "options": [
          "parameters",
          "statements"
        ]
      },
      "array-type": false,
      "arrow-return-shorthand": true,
      "curly": true,
      "deprecation": {
        "severity": "warning"
      },
      "eofline": true,
      "import-blacklist": [
        true,
        "rxjs/Rx"
      ],
      "import-spacing": true,
      "indent": {
        "options": [
          "spaces"
        ]
      },
      "max-classes-per-file": false,
      "max-line-length": [
        true,
        140
      ],
      "member-ordering": [
        true,
        {
          "order": [
            "static-field",
            "instance-field",
            "static-method",
            "instance-method"
          ]
        }
      ],
      "no-console": [
        true,
        "debug",
        "info",
        "time",
        "timeEnd",
        "trace"
      ],
      "no-empty": false,
      "no-inferrable-types": [
        true,
        "ignore-params"
      ],
      "no-non-null-assertion": true,
      "no-redundant-jsdoc": true,
      "no-switch-case-fall-through": true,
      "no-var-requires": false,
      "object-literal-key-quotes": [
        true,
        "as-needed"
      ],
      "quotemark": [
        true,
        "single"
      ],
      "semicolon": {
        "options": [
          "always"
        ]
      },
      "space-before-function-paren": {
        "options": {
          "anonymous": "never",
          "asyncArrow": "always",
          "constructor": "never",
          "method": "never",
          "named": "never"
        }
      },
      "typedef": [
        true,
        "call-signature"
      ],
      "typedef-whitespace": {
        "options": [
          {
            "call-signature": "nospace",
            "index-signature": "nospace",
            "parameter": "nospace",
            "property-declaration": "nospace",
            "variable-declaration": "nospace"
          },
          {
            "call-signature": "onespace",
            "index-signature": "onespace",
            "parameter": "onespace",
            "property-declaration": "onespace",
            "variable-declaration": "onespace"
          }
        ]
      },
      "variable-name": {
        "options": [
          "ban-keywords",
          "check-format",
          "allow-pascal-case"
        ]
      },
      "whitespace": {
        "options": [
          "check-branch",
          "check-decl",
          "check-operator",
          "check-separator",
          "check-type",
          "check-typecast"
        ]
      },
      "component-class-suffix": true,
      "contextual-lifecycle": true,
      "directive-class-suffix": true,
      "no-conflicting-lifecycle": true,
      "no-host-metadata-property": true,
      "no-input-rename": true,
      "no-inputs-metadata-property": true,
      "no-output-native": true,
      "no-output-on-prefix": true,
      "no-output-rename": true,
      "no-outputs-metadata-property": true,
      "template-banana-in-box": true,
      "template-no-negated-async": true,
      "use-lifecycle-interface": true,
      "use-pipe-transform-interface": true,
      "directive-selector": [
        true,
        "attribute",
        "app",
        "camelCase"
      ],
      "component-selector": [
        true,
        "element",
        "app",
        "kebab-case"
      ]
    }
}`;
  
const karmaFile = `
// Karma configuration file, see link for more information
// https://karma-runner.github.io/1.0/config/configuration-file.html

module.exports = function (config) {
  config.set({
    basePath: '',
    frameworks: ['jasmine', '@angular-devkit/build-angular'],
    plugins: [
      require('karma-jasmine'),
      require('karma-chrome-launcher'),
      require('karma-jasmine-html-reporter'),
      require('karma-coverage-istanbul-reporter'),
      require('@angular-devkit/build-angular/plugins/karma')
    ],
    client: {
      clearContext: false // leave Jasmine Spec Runner output visible in browser
    },
    coverageIstanbulReporter: {
      dir: require('path').join(__dirname, './coverage/machote'),
      reports: ['html', 'lcovonly', 'text-summary'],
      fixWebpackSourcePaths: true
    },
    reporters: ['progress', 'kjhtml'],
    port: 9876,
    colors: true,
    logLevel: config.LOG_INFO,
    autoWatch: true,
    browsers: ['Chrome'],
    singleRun: false,
    restartOnFileChange: true
  });
};
`;
/**
 * Regresa los archivos de pruebas para angular
 * @returns {[ArchivoTexto]}
 */
export function getPruebas() {
    return [ 
        new ArchivoTexto('tslint.json', tslinFile),
        new ArchivoTexto('karma.conf.js', karmaFile)
    ];
}