import { config } from "../../../config.js";
import { getCsslink } from "../../../exports/export-web-component.js";
import { ArchivoTexto } from "./utils.js";

export function getFont(tipo) {
    if (tipo == 'sketchy') {
        return `font-family: 'Neucha', cursive;`;
    }
    return '';
}
export function getStyle(tipo) {
    if (tipo == 'sketchy') {
        return `
    <link href="https://fonts.googleapis.com/css2?family=Cabin+Sketch&display=swap" rel="stylesheet">
    <link href="https://fonts.googleapis.com/css2?family=Neucha&display=swap" rel="stylesheet">
        `;
    }
    return '';
}
/**
 * Regresa el ArchivoTexto del index w3.css
 * @param {string} nombre titulo de la pagina
 * @param {string} tipo css del proyecto
 */
export function getW3Index(nombre, tipo) {
    let css = getCsslink(tipo);
    let styleExtra = getStyle(tipo);    
    return new ArchivoTexto('src/index.html', `
<!DOCTYPE html>
<html lang="es">
<head>
    <meta charset="UTF-8">
    <meta name="viewport" content="width=device-width, initial-scale=1.0">
    <link rel="stylesheet" href="${css}">
    <link rel="stylesheet" href="${config.fontAwesome}">
${styleExtra}
    <title>${nombre}</title>
</head>
<body>
    <div id="app"></div>
    <script src="main.js" ></script>
</body>
</html>
`)
};