import { Modal } from '../../vanilla/utils/modal/modal.js';
import { swapElementsDom } from '../../vanilla/utils/dom/util-dom.js';
let contDiv = 1;

export function getBackgroundColor(tipo) {
    if (tipo == 'superhero') {
        return '#2B3E50 !important';
    }    
    return '';
}
export function createContenido(nombre, tag, propiedades, shadowRoot, tipo = 'w3css'){
    let div = document.createElement('div');
    div.classList.add('w3-block');
    div.classList.add('w3-white');
    div.classList.add('w3-border');
    div.classList.add('w3-hover-border-blue');
    const color = getBackgroundColor(tipo);
    div.style.cssText = `min-height: 100px; background: ${color};`;
    div.id = "div" + contDiv++;
    div.setAttribute('draggable', 'true');
    div.setAttribute('contenedor',"true");    
    div.innerHTML = `
    <div class="basura w3-right" style="top: -4px; position: relative;"><i class="fa fa-trash" id="eliminar"></i></div>
    <div 
        style="top: 0; position: relative; width: fit-content;" class="w3-teal w3-hover-blue w3-tiny">
        ${nombre}
    </div>
    `;
    let tagItem = document.createElement(tag);
    tipo == "null" ? tipo = 'w3css' : '';
    tagItem.setAttribute('tipo', tipo)
    tagItem.setAttribute('componente', 'exportar');
    if (tagItem.setPropiedades) {
        tagItem.setPropiedades(propiedades);
    }
    if (shadowRoot) {
        tagItem.setShadowRoot(shadowRoot);
    }
    div.append(tagItem);
    let dlg = new Modal();
    dlg.innerHtml = 'Desea eliminar este componente?';
    dlg.titulo = 'Confirmación';
    const eliminar = div.querySelector('#eliminar');    
    eliminar.addEventListener('click', (e) => {        
        const componente = e.target.parentNode.parentNode;        
        dlg.open().then(r=> {            
            componente.remove();
            propiedades.setItem('', null);
        })
    });           
    return div;
}
export function swapElements(obj1, obj2) {
    swapElementsDom(obj1, obj2);
}