import { w3CodeColor } from "../../../../utils/colores/colores.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding codigo w3-margin-top">
<div class="w3-col s12">
    <div class="w3-panel w3-card w3-light-grey"> 
    <span class="w3-button w3-blue w3-right" id="actualiza" no-usar>actualiza</span>
    <h4 propiedades>Código js</h4>
    <div></div>
    <div class="w3-code jsHigh notranslate" contenteditable="true" editor>
        let frutas = [];<br>
        frutas[0] = "Platano";<br>
        frutas[1] = "Manzana";<br>
        frutas[2] = "Mango";<br>
        frutas[3] = "Naranja";<br>
    </div>
    </div>    
</div>
</div>

`;
const style = '';
class TextoCodigo extends BaseW3CSS {
    constructor() {
        super();
        this.render();        
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;
        this.addListenerActualiza();
        w3CodeColor(shadowRoot);
    }

    addListenerActualiza() {
        let boton = this.shadowRoot.querySelector('#actualiza');
        boton.addEventListener('click', (ev) => {
            this.actulizaCodigo();
        });
    }

    actulizaCodigo() {
        let editor = this.shadowRoot.querySelector('[editor]');
        let texto = editor.innerText.split('\n');
        editor.innerHTML = '';
        for(let linea of texto){
            editor.innerHTML += linea + '<br>';
        }
        w3CodeColor(this.shadowRoot);
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.codigo');
        const div = original.cloneNode(true);
        this.removerNoUsar(div);
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.codigo')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    addAllListenersShadowRoot() {
        this.addListenersPropiedades();
        this.addListenerActualiza();
    }
}

customElements.define("x-codigo-js", TextoCodigo);