import { config } from "../../../../config.js";
import { ModalForm } from "../../../../utils/modal/modal-form.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-container">
<button class="w3-button w3-xlarge w3-circle w3-teal w3-right" id ="add-code">
    <i class="fa fa-pencil-square-o"></i>
</button>
</div>
<div custom-item>
</div>
`;
const style = '';
let dlg = new ModalForm();
dlg.titulo = 'Código html';
class CustomItem extends BaseW3CSS {
    constructor() {
        super();
        this.render();        
    }

    connectedCallback() {
        
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `  
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
        this.div = shadowRoot.querySelector('[custom-item]');
        this.addListeners();
    }

    addListeners() {
        const btn = this.shadowRoot.querySelector('#add-code');
        btn.addEventListener('click', e => {
            this.agregarCodigo();
        })
    }

    agregarCodigo() {                
        dlg.innerHtml = `
        <div class="w3-container">
            <label>Codigo html5</label>
            <br>
            <x-codigo modo="text/html" 
                tema="material" 
                codigo='${this.div.innerHTML.replace(/'/g,'´')}' 
                name="codigo" 
                dato-salida />            
        </div>
        `;
        dlg.open().then(re => {
            this.div.innerHTML = re.data.codigo.replace(/´/g,"'");
            this.addListenersPropiedades();
        });
    }

    getHtml() {
        const original = this.div;
        const div = original.cloneNode(true);
        this.removerNoUsar(div);
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.div;  
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    addAllListenersShadowRoot() {
        this.addListenersPropiedades();
        this.addListeners();
    }
}

customElements.define("x-custom-item", CustomItem);