import { config } from "../../../../config.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding alerta w3-margin-top">
<div class="w3-col s12">
    <div class="w3-panel w3-yellow w3-border" propiedades>
        <h3 contenteditable="true" propiedades><i class="fa fa-warning" propiedades></i> Precaución!</h3>
        <p contenteditable="true" propiedades>El amarillo suele indicar una advertencia que podría necesitar atención.</p>
    </div>
</div>
</div>

`;
const style = '';
class AlertaTexto extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.alerta')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.alerta')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-alerta", AlertaTexto);