import { config } from '../../config.js';
import { getDB } from '../../utils/db/db.js';
import { loadCSSExtra } from '../../utils/html/ventana.js';
import { addWindowConParametros } from '../../utils/ventana/agregarApps.js';
const template = /*html*/`
<x-bar-menu color="w3-blue-grey"></x-bar-menu>
<div class="w3-block" style="height: 100vh; padding: 5%;">
    <h1 titulo>W3 Estudio</h1>
    <x-menu-aero menu></x-menu-aero>
</div>
`;

class W3EMenuInicio extends HTMLElement {
    constructor() {
        super();
        this.render();
        this.addListeners();
        this.db = getDB();
        this.idProyecto = null;
    }

    render() {
        let shadowRoot = this.attachShadow({ mode: 'open'});        
        shadowRoot.innerHTML = `
            <link rel="stylesheet" href="${config.w3css}">
            <link rel="stylesheet" href="${config.fontAwesome}">
            ${template}
        `;
        const menus = [
            {
                titulo: 'Grupos',
                icono: 'fa-users',
                color: 'w3-blue',
                appTag: 'x-paginador-grupos'
            },{
                titulo: 'Usuario',
                icono: 'fa-user-circle',
                color: 'w3-indigo',
                appTag: 'x-paginador-usuarios'
            },{
                titulo: 'Vistas',
                icono: 'fa-table',
                color: 'w3-teal',
                appTag: 'x-paginador-vistas'
            },{
                titulo: 'Headers',
                icono: 'fa-minus',
                color: 'w3-green',
                appTag: 'x-paginador-headers'
            },{
                titulo: 'Footers',
                icono: 'fa-paw',
                color: 'w3-pink',
                appTag: 'x-paginador-footers'
            },{
                titulo: 'Configuraciones',
                icono: 'fa-gear',
                color: 'w3-purple',
                appTag: 'x-configuracion-proyecto'
            },{
                titulo: 'Exportar',
                icono: 'fa-cloud-download',
                color: 'w3-red',
                appTag: 'x-w3e-exportar-proyecto'
            }
        ];
        this.menu = shadowRoot.querySelector('[menu]');
        this.menu.setMenu(menus);
        this.menuBar = shadowRoot.querySelector('x-bar-menu');        
    };

    addListeners() {
        this.menu.addEventListener('select', ev => {
            this.menuSeleccionado(ev.detail.menu);
        });
    }

    menuSeleccionado(menu) {
        let app = addWindowConParametros(menu.appTag, menu.titulo, null, true, {idproyecto: this.idProyecto});
    }

    connectedCallback() {
        this.idProyecto = parseInt(this.getAttribute('idproyecto'));
        this.db.get('proyectos', this.idProyecto).then(data => {
            let titulo = this.shadowRoot.querySelector('[titulo]');
            titulo.innerHTML = data.nombre;
            loadCSSExtra(data.configuracion.css);
            this.menuBar.setItem({
                titulo: 'Proyecto - ' + data.nombre,
                icono: 'file-code-o',
                acciones: []
            });
        });        
    }
}

customElements.define('x-w3e-menu-inicio', W3EMenuInicio);

