import { getDB } from "../../../../utils/db/db.js";
import { jsonToListaObjectos } from "../../../editor-caminos/itemsCanvas.js";
import { JSArchivo, ArchivoTexto, creaArbol, getNombreArchivo, getCamino } from "../../vue/utils.js";
import { toCammelCase } from "../../../../exports/nombres.js";

/**
 * Regresa una lista de rutas
 * @param {string} ruta Ruta a la que se le sumara el nuevo contexto
 * @param {nodo} nodo Nodo del flujo de rutas.
 */
export function fixRutasAngular(rutas) {        
    for(let ruta of rutas) {        
        if (ruta.ruta[0] == '/'){
            ruta.ruta = ruta.ruta.replace('/', '');
        }
    }    
    return rutas;
}

/**
 * Genera una arreglo de ArchivsTexto con todas las rutas
 * @param {number} idProyecto Es el id de proyecto
 * @returns {[ArchivoTexto]}
 */
export async function generarRutas(idProyecto) {
    let archivos = [];
    let db = getDB();
    let grupos = await db.list('grupos');
    grupos = grupos.filter(grupo => {return grupo.idProyecto == idProyecto});
    let caminos = await db.list('caminos');
    for(let i=0; i<grupos.length; i++){                
        let camino = getCamino(grupos[i].id, caminos);
        const flujo = jsonToListaObjectos(camino.flujo);
        let nombre = grupos[i].nombre
            .trim()
            .toLowerCase()
            .replace(/ /g,'-');
        let rutaInicial= '/' + flujo[0].contexto.trim()
            .toLowerCase()
            .replace(/ /g,'-');
        //creamos un arreglo de rutas definidas por el primer nodo
        let rutas = [{ruta: rutaInicial, nodo: flujo[0]}]
            .concat(creaArbol(rutaInicial, flujo[0].caminoInicial));
        rutas = fixRutasAngular(rutas);
        const modulorutas = await creaModuloRutas(nombre, rutas);
        archivos = archivos.concat(modulorutas);
    }    
    return archivos;
}

async function creaModuloRutas(nombre, rutas) {
    let db = getDB();
    let imports = new Set();
    let importstext = '';
    let paths = '';
    let declaraciones = '';
    let archivos = [];
    let idHeader = parseInt(rutas[0].nodo.header);
    let idFooter = parseInt(rutas[0].nodo.footer);
    let header = null;
    let footer = null;
    if (idHeader) {
        header = await db.get('headers', idHeader);
    }
    if (idFooter) {
        footer = await db.get('footers', idFooter);
    }
    let nombreHeader = header ? getNombreArchivo(header.nombre) : '';
    let nombreHFooter = footer ? getNombreArchivo(footer.nombre) : '';
    for (let ruta of rutas) {
        let nombreComponente = toCammelCase( ruta.nodo.nombre.trim().replace(/ /g,'-').toLowerCase()) + 'VistaComponent';
        const vistaCamino = await creaVista(ruta, nombre, 
            nombreComponente, nombreHeader, nombreHFooter);
        archivos.push(vistaCamino);
        paths += `        { path: '${ruta.ruta}', component: ${nombreComponente} } ,\n`;
        let imp = `import { ${nombreComponente} } from './${getNombreArchivo(ruta.nodo.nombre)}-vista.component';`;
        if (!imports.has(imp)) {
            imports.add(imp);
            importstext += imp+'\n';
            declaraciones += `      ${nombreComponente},\n`;
        }
    }
    let archivoRutas = creaRouting(nombre, importstext, paths);
    let moduloRuta = creaModuloRouting(nombre, importstext, declaraciones);
    archivos.push(archivoRutas);
    archivos.push(moduloRuta);
    return archivos;
}

function creaRouting(nombre, importstext, paths) {
    return new JSArchivo(
        `src/flujos/${nombre}/${getNombreArchivo(nombre)}-routing.module.ts`, `
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
${importstext}

const routes: Routes = [
${paths}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ${toCammelCase(nombre)}RoutingModule { }    
`);
}

function creaModuloRouting(nombre, importstext, declaraciones) {
    return new JSArchivo(
        `src/flujos/${nombre}/${getNombreArchivo(nombre)}.module.ts`, `
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ${toCammelCase(nombre)}RoutingModule } from './${getNombreArchivo(nombre)}-routing.module';
import { FootersModule } from 'src/footers/footers.module';
import { VistasModule } from 'src/vistas/vistas.module';
import { HeadersModule } from 'src/headers/headers.module';
//Vistas
${importstext}

@NgModule({
    declarations: [
${declaraciones}
    ],
    imports: [
        CommonModule,
        VistasModule,
        FootersModule,
        HeadersModule,
        ${toCammelCase(nombre)}RoutingModule,
    ]
})
export class ${toCammelCase(nombre)}Module { }
`);
}

async function creaVista(ruta, nombre, nombreComponente, header, footer) {
    let db = getDB();
    let idVista = parseInt(ruta.nodo.vista);
    let vista = null;
    if (idVista) {
        vista = await db.get('vistas', parseInt(ruta.nodo.vista));
    }
    let nombreArchivo = `src/flujos/${nombre}/${getNombreArchivo(ruta.nodo.nombre)}-vista.component.ts`;
    //nombre del selector
    let tag = vista ? vista.nombre
            .toLowerCase()
            .replace(/ /g, '-') : '';
    let tagHeader = header ? `<${header}></${header}>` : '';
    let tagFooter = footer ? `<${footer}></${footer}>` : '';
    let tagContent = vista ? `<${tag}></${tag}>` : '';
    return new JSArchivo( nombreArchivo,
    `
import { Component, OnInit } from '@angular/core';

const template = \`
<div>
    ${tagHeader}
    ${tagContent}
    ${tagFooter}
</div>    
\`;

@Component({
    template: template,  
})
export class ${nombreComponente} implements OnInit {
    
    constructor() { }

    ngOnInit(): void {
    }

}    
    `);
}