import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding titulo-h4">
<div class="w3-col s12" contenedor>    
    <h4 propiedades contenteditable="true">Título</h4>    
</div>
</div>

`;
const style = '';
class TituloH4 extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;        
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.titulo-h4')    
        const div = original.cloneNode(true);  
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.titulo-h4')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-titulo-h4", TituloH4);