import { ModalGet } from "../../../../utils/modal/modal-get.js";
import { config } from "../../../../config.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding w3-padding-64 w3-container grid-descripcion w3-light-grey" propiedades>
  <div class="w3-content">
    <div class="w3-twothird">
      <h2 contenteditable="true" propiedades>Lorem Ipsum</h2>
      <h5 class="w3-padding-32" contenteditable="true" propiedades>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</h5>

      <p class="w3-text-grey" contenteditable="true" propiedades>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Excepteur sint
        occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
        laboris nisi ut aliquip ex ea commodo consequat.</p>
    </div>

    <div class="w3-third w3-center">
        <i class="fa fa-html5  w3-padding-64 w3-text-red size-icon"></i>
    </div>
  </div>
</div>
`;
const style = `
.img-grid{
    width: 100%;
    border-radius: 5px;
}
.size-icon{
    font-size:200px;
}
`;

class GridDescripcion extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `          
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
        //this.addListeners();            
    }

    addListeners(){        
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.grid-descripcion')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.grid-descripcion')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-grid-descripcion", GridDescripcion);