import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-container w3-red w3-center titulo-jumbo" contenteditable="true" propiedades>
    <h1 class="w3-margin w3-jumbo">Página de inicio</h1>
    <p class="w3-xlarge">Usando w3.css</p>
</div>
`;
const style = '';
class TituloJumbo extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <style>${style}</style>
            ${template}
        `;              
    }

    getHtml() {    
        const div = this.shadowRoot.querySelector('.titulo-jumbo')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.titulo-jumbo')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-titulo-jumbo", TituloJumbo);