import { dataCss, dataJs } from '../vistas/codigos-iniciales.js';
import { PaginadorVistas } from '../vistas/paginador-vistas.js';

class PaginadorFooters extends PaginadorVistas {
    constructor() {
        super();
        this.tabla = 'footers';
        this.titulo = 'Footers';
        this.color = 'pink';
        this.icono = 'paw';
    }
    //sobreescribimos
    addItem() {
        let item = {
            nombre: 'Footer',
            idProyecto: this.idProyecto
        };
        this.db.post(this.tabla, item).then(data => {            
            this.lista.push(data);
            this.paginador.setLista(this.lista);
            this.db.post('metadataVistas', {
                id: data.id,
                codigo: dataJs,
                estilo: dataCss,
                componentes: []
            }, false);
        });        
    }
}

customElements.define('x-paginador-footers', PaginadorFooters);