import {config} from '../../../../config.js';
import { Modal } from '../../../../utils/modal/modal.js';
import { swapElementsDom } from '../../../../utils/dom/util-dom.js';
import { ModalForm } from '../../../../utils/modal/modal-form.js';
import { BaseW3CSS } from '../../base/componente-w3css.js';

const template = /*html*/`
<div formulario class="w3-margin-top">    
    <div class="w3-button w3-blue w3-large w3-right w3-circle" id="add" no-usar>
        <i class="fa fa-plus"></i>
    </div>
    <form onsubmit="return false" propiedades>
    <div class="w3-container">     
        <div id="items-div" class="w3-row"></div> 
    </div>
    <div class="w3-container">
    <button class="w3-button w3-blue w3-margin-top"
        contentEditable="true" propiedades>Guardar</button>
    </div>
    </form>
</div>
`;

const style = /*css*/`
input:invalid {
    outline: solid 1px red;
    background: pink;
}
input[type="color"] {
    padding: 0;
    height: 40px;
}
`;

let idInput = Date.now();

class FormularioSimple extends BaseW3CSS {
    constructor() {
        super();
        this.render();
        this.addListeners();
    }

    render(){
        super.render();        
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>            
            ${template}
        `;
        this.formulario = shadowRoot.querySelector('#items-div');        
    }

    addAllListenersShadowRoot() {
        this.formulario = this.shadowRoot.querySelector('#items-div');
        this.addListenersPropiedades();        
        this.addListeners();
        let divs = this.formulario.querySelectorAll('[input-div]');
        for(let div of divs){
            this.addListenersDivInput(div);
        }
    }

    addListenersDivInput(div) {
        let arriba = div.querySelector('#arriba');
        let abajo = div.querySelector('#abajo');
        let eliminar = div.querySelector('#eliminar');
        this.addListenersBotonesInput(div, arriba, abajo, eliminar);
    }

    addListeners() {
        let btn = this.shadowRoot.querySelector('#add');
        btn.addEventListener('click', ev => {
            this.agregaInput();
        });
    }
    agregaInput() {
        let dlg = new ModalForm();
        dlg.titulo = 'Tipo de input';
        dlg.innerHtml  = `
        <div>
        <label>Tipo de input</label>
        <select dato-salida name="tipo" class="w3-select">
                <option value="text">Texto</option>
                <option value="password">Password</option>
                <option value="number">Número</option>
                <option value="date">Fecha</option>
                <option value="time">Hora</option>
                <option value="color">Color</option>
                <option value="radio">Radio</option>
                <option value="checkbox">Checkbox</option>
                <option value="label">Label</option>
                <option value="textarea">Textarea</option>
        </select>
        </div>
        `;
        dlg.open().then(data => {
            if(data.data.tipo != 'textarea') {
                if (data.data.tipo == 'label') {
                    this.addLabel();
                }else{
                    this.addInput(data.data.tipo);
                }
            }else if(data.data.tipo == 'textarea') {
                this.addTextArea();
            }
        });
    }

    addLabel() {
        let div = document.createElement('div');
        div.setAttribute('input-div', '');
        let botones = this.addBotonesInput(div);                
        let salto = document.createElement('br');              
        let label = document.createElement('label');
        label.innerHTML = ' Etiqueta';
        label.contentEditable = 'true';       
        label.setAttribute('propiedades', ''); 
        div.appendChild(botones);
        div.appendChild(salto);        
        div.appendChild(label);        
        div.id = idInput++;
        div.classList.value = 'w3-col s12 m12';
        div.setAttribute('propiedades', '');
        this.formulario.appendChild(div);        
        this.addListenersPropiedadesItem(label);
        this.addListenersPropiedadesItem(div); 
    }

    addTextArea() {
        let div = document.createElement('div');
        div.setAttribute('input-div', '');
        let botones = this.addBotonesInput(div);        
        let input = document.createElement('textarea');
        let salto = document.createElement('br');
        input.classList.value = 'w3-input w3-border';
        input.style.resize = 'none';
        input.setAttribute('propiedades', '');
        input.setAttribute('input-formulario', '');        
        let label = document.createElement('label');
        label.innerHTML = ' Etiqueta textarea';
        label.contentEditable = 'true';       
        label.setAttribute('propiedades', ''); 
        div.appendChild(botones);
        div.appendChild(salto);        
        div.appendChild(label);
        div.appendChild(input);
        div.id = idInput++;
        div.classList.value = 'w3-col s12 m12';
        div.setAttribute('propiedades', '');
        this.formulario.appendChild(div);
        this.addListenersPropiedadesItem(input);
        this.addListenersPropiedadesItem(label);
        this.addListenersPropiedadesItem(div); 
    }

    addInput(tipo){
        let div = document.createElement('div');
        div.setAttribute('input-div', '');
        let botones = this.addBotonesInput(div);        
        let input = document.createElement('input');
        let salto = document.createElement('br');
        input.classList.value = 'w3-input w3-border';
        if (tipo == 'radio') {
            input.classList.value = 'w3-radio';
        }else if (tipo == 'checkbox') {
            input.classList.value = 'w3-check';
        }
        input.setAttribute('propiedades', '');
        input.setAttribute('input-formulario', '');
        input.type = tipo;
        let label = document.createElement('label');
        label.innerHTML = ' Etiqueta ' + tipo;
        label.contentEditable = 'true';       
        label.setAttribute('propiedades', ''); 
        div.appendChild(botones);
        div.appendChild(salto);
        if (tipo == 'radio' || tipo == 'checkbox') {
            div.appendChild(input);
            div.appendChild(label);
        } else {
            div.appendChild(label);
            div.appendChild(input);
        }
        
        div.id = idInput++;
        div.classList.value = 'w3-col s12 m12';
        div.setAttribute('propiedades', '');
        this.formulario.appendChild(div);
        this.addListenersPropiedadesItem(input);
        this.addListenersPropiedadesItem(label);
        this.addListenersPropiedadesItem(div);
    }

    addListenersPropiedadesItem(div) {        
        div.addEventListener('click', (e) => {
            this.propiedades.setItem(this.tagName, e.target);
        });
    }

    addBotonesInput(div) {
        let botones = document.createElement('div');
        let arriba = document.createElement('span');
        arriba.classList.value = 'w3-button w3-xlarge w3-circle w3-black';
        arriba.id = 'arriba';
        arriba.setAttribute('no-usar', '');
        let abajo = document.createElement('span');
        abajo.id = 'abajo';
        abajo.classList.value = 'w3-button w3-xlarge w3-circle w3-black';
        abajo.setAttribute('no-usar', '');
        let eliminar = document.createElement('span');
        eliminar.id = 'eliminar';
        eliminar.classList.value = 'w3-button w3-xlarge w3-circle w3-red';
        eliminar.setAttribute('no-usar', '');
        arriba.innerHTML = '<i class="fa fa-arrow-up"></i>';
        abajo.innerHTML = '<i class="fa fa-arrow-down"></i>';
        eliminar.innerHTML = '<i class="fa fa-trash"></i>';
        botones.appendChild(arriba);        
        botones.appendChild(abajo);
        botones.appendChild(eliminar);
        this.addListenersBotonesInput(div, arriba, abajo, eliminar);
        botones.classList.value= 'w3-right';
        botones.style.transform = 'scale(0.5)';
        botones.setAttribute('no-usar', '');
        return botones;
    }
    addListenersBotonesInput(div, arriba, abajo, eliminar) {
        arriba.addEventListener('click', ev => {
            this.swapArriba(div);
        });
        abajo.addEventListener('click', ev => {
            this.swapAbajo(div);
        });
        eliminar.addEventListener('click', ev => {
            let dlg = new Modal();
            dlg.titulo = 'Confirmación';
            dlg.innerHtml = 'Desea Eliminar el input';
            dlg.open().then(e => {
                div.remove();
            });
        });
    }
    /*busca el elemento que esta arriba de este y se intercambia de lugar*/
    swapArriba(div){
        let superior = null;
        for (let item of this.formulario.childNodes){
            if (item.id == div.id) {
                break;
            }
            if (item.tagName == 'DIV') {
                superior = item;
            }
        }        
        if (superior) {
            swapElementsDom(superior, div);
        }
    }

    swapAbajo(div) {
        let inferiror = null;
        let estaDiv = false;
        for (let item of this.formulario.childNodes){
            if (estaDiv  && item.tagName == 'DIV') {
                inferiror = item;
                break;
            }
            if (item.id == div.id) {
                estaDiv = true;
            }
        }        
        if (inferiror) {
            swapElementsDom(inferiror, div);
        }
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('[formulario]');
        const div = original.cloneNode(true);
        this.removerNoUsar(div);
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }
}

customElements.define('x-formulario-simple', FormularioSimple);