import { ModalGet } from "../../../../utils/modal/modal-get.js";
import { BaseBootstrap } from "../../base/componente-bootstrap.js";
const template = /*html*/`
<div class="row imagen">
<div class="col-sm-2 col-md-2 espacio">        
</div>
<div class="col-sm-8 col-md-8">
    <img src="./img/libres/lago.jpg" class="articulo-img">
</div>
</div>

`;
const style = `
.articulo-img{
    width: 100%;
    border-radius: 5px;
}
`;
class BotonBS extends BaseBootstrap {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `
            <style>${style}</style>
            ${template}
        `;
        this.addListeners();
    }

    addListeners(){
        const img = this.shadowRoot.querySelector('img');
        img.addEventListener('click', this.onChangeImg.bind(this));
    }

    onChangeImg(){
        const img = this.shadowRoot.querySelector('img');
        let dlg = new ModalGet('Dame url de imagen', img.src);
        dlg.open().then(dlg => {
            img.src = dlg.data;
        });
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.imagen')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.imagen')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    addAllListenersShadowRoot() {
        this.addListenersPropiedades();
        this.addListeners();
    }
}

customElements.define("x-bs-imagen", BotonBS);