import  { config } from "../../config.js";
import { getDB } from "../../utils/db/db.js";
import { SplashScreen } from "../../utils/modal/splash-screen.js";
import { addNotificacion, NOTIFICACION } from "../../utils/notificacion/notificaciones.js";
import { addWindowConParametros } from "../../utils/ventana/agregarApps.js";

const template = /*html*/`
<x-bar-menu color="w3-black" buscar></x-bar-menu>
<x-paginador-files titulo="Proyectos" vista-previa="x-proyecto-vista-previa"></x-paginador-files>
`;

const style = /*css*/`
#buscar {
    transition: width 0.4s ease-in-out;
    font-size: small;
    width: 100px;    
}
#buscar:focus {
    width: 300px;
}
`;

class PaginadorProyectos extends HTMLElement {
    constructor() {
        super();
        this.db = getDB();  
        this.lista = [];
    }
    
    connectedCallback() {
        this.render();        
        this.cargaLista();
        let splash = new SplashScreen();
        splash.titulo = 'Proyecto';
        splash.open();
    }

    cargaLista() {
        this.db.list('proyectos').then(data => {
            this.lista = data;
            this.paginador.setLista(data);
        });
    }

    getTipoImagen(item) {
        if (item.configuracion.css == 'w3css') {
            return './img/icon/text-file-icon-w3css.svg';
        }
        return './img/icon/text-file-icon-bootstrap.svg';
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <link rel="stylesheet" href="${config.fontAwesome}">
        <style>${style}</style>
        ${template}
        `;
        this.paginador = shadowRoot.querySelector('x-paginador-files');
        this.paginador.setLista(this.lista);
        this.paginador.setGetTipoImagen(this.getTipoImagen);
        this.menu = shadowRoot.querySelector('x-bar-menu');
        this.menu.setItem({
            titulo: 'Proyectos',            
            icono: 'css3',
            acciones: [{
                titulo: 'Nuevo',
                icono: 'fa-file-o',
            }]
        });
        this.addListeners();              
    }

    addListeners() {
        this.paginador.addEventListener('onVistaPrevia', (e) => {
            const r = e.detail;
            r.vistaPrevia.setItem(r.item);
            r.vistaPrevia.addEventListener('actualizar', ev => {
                let data = ev.detail.item;
                this.actualizaLista(data);
            });
        });
        this.paginador.addEventListener('onOpenItem', (e) => {
            const r = e.detail;
            let app = addWindowConParametros('x-w3e-menu-inicio', r.item.nombre, 
                null, true, {idProyecto: r.item.id});            
        });
        const padre = this.parentNode;
        padre.addEventListener('nuevo', (ev) => {
            this.addItem();
        });
        this.menu.addEventListener('accion', ev => {
            let data = ev.detail;
            if (data.accion.titulo == 'Nuevo') {
                this.addItem();
            }
        });
        this.menu.addEventListener('buscar', ev => {
            let data = ev.detail;
            this.buscaElementos(data.value);
        });
    }

    buscaElementos(nombre) {        
        let buscar = [];
        nombre = nombre.trim();
        if (nombre == '') {
            this.paginador.setLista(this.lista);
            return;
        }
        nombre = nombre.toUpperCase();
        for(let item of this.lista){
            if (item.nombre.toUpperCase().indexOf(nombre) >= 0){
                buscar.push(item);
            }
        }
        this.paginador.setLista(buscar);
    }

    buscaIndexItem(id) {
        return this.lista.findIndex(item => {
            return item.id == id;
        });
    }
    
    eliminaItem(id) {
        const i = this.buscaIndexItem(id);
        if (i<0) return;
        console.log(id);
        this.db.delete('vistas', id).then(data => {
            this.lista.splice(i,1);
            this.paginador.setLista(this.lista);
            this.resetBuscador();        
        });
    }

    resetBuscador() {
        let buscar = this.shadowRoot.querySelector('#buscar');
        buscar.value = '';
    }

    actualizaLista(data){
        const i = this.buscaIndexItem(data.id);
        if (i<0) return;
        this.lista[i].nombre = data.nombre;
        this.lista[i].descripcion = data.descripcion;

        this.db.put('proyectos', this.lista[i]).then(data => {                        
            this.paginador.setLista(this.lista);
            this.resetBuscador();
        });
    }

    addItem() {
        let item = {
            nombre: 'Proyecto',
            descripcion: '',
            configuracion: {
                css: 'w3css'
            } 
        }
        this.db.post('proyectos', item).then(data => {            
            this.lista.push(data);
            this.paginador.setLista(this.lista);
        });        
    }
}
customElements.define('x-paginador-proyectos', PaginadorProyectos);