export const dataJs = `//define el código del componente
{
    //define las propiedades del componente
    props: {
        titulo: ''
    },
    //define las variables del componente
    data: {
        mensaje: 'Hola mundo'
    },
    //define los metodos del componente
    metodos: {
        holaMundo() {
            console.log(this.mensaje);
        }
    },
    //Aqui se pondran las instruciones que inicializan al componente
    init() {
        this.holaMundo();
    }
}
`;
export const dataCss = `/*define los estilos del componente*/
.hola-mundo{
    color: red;
}
`;