import { createContenido } from "../../../../../appVue/ide/utils.js";
import { config } from "../../../../config.js";
import { swapElementsDom } from "../../../../utils/dom/util-dom.js";
import { Modal } from "../../../../utils/modal/modal.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
import { getComponentes, getMetodosWebcomponente } from "./utils.js";
const template = /*html*/`
<div class="w3-row" div-x>
<div class="w3-col s12">
    <div class="w3-border w3-hover-border-red" div-custom propiedades style="min-height: 50px">
    </div>
</div>
</div>
`;
const style = '';
/**
 * 
 * @param {HTMLElement} div 
 */
function creaDiv(original) {
    const div = document.createElement('div');
    div.classList.value = 'w3-row';    
    let custom = original.querySelector('[div-custom]');
    let clases = custom.classList.value;
    clases = clases.replace('w3-border', '');
    let codigo = getComponentes(custom, 'html');
    div.innerHTML = `
    <div class="w3-col s12">
        <div class="${clases}">
            ${codigo}
        </div>
    </div>
    `;
    return div;
}
class DivCustom extends BaseW3CSS {
    constructor() {
        super();
        this.render();
        /** Es el item que se mueve*/
        this.item1 = null;
        /** Es el item que tiene ensima al item que se mueve*/
        this.item2 = null;
        this.addListeners();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        this.codigoInicial = shadowRoot.innerHTML;
        shadowRoot.innerHTML += `            
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;
    }

    setShadowRoot(shadowRoot){         
        this.shadowRoot.innerHTML = shadowRoot;
        let componentes = this.shadowRoot.querySelectorAll('[crear-componente]');
        for(let c of componentes) {            
            let tag = c.getAttribute('crear-componente').toLowerCase();
            let tagItem = document.createElement(tag);
            tagItem.setAttribute('tipo', c.getAttribute('tipo'))
            tagItem.setPropiedades(this.propiedades);
            tagItem.setShadowRoot(c.innerHTML);
            tagItem.setAttribute('componente', 'exportar');
            this.addListenersItem(tagItem);
            c.after(tagItem);
            c.remove();
        }        
        this.addAllListenersShadowRoot();
    }

    addAllListenersShadowRoot() {
        super.addAllListenersShadowRoot();
        this.addListeners();
        let contenedores = this.shadowRoot.querySelectorAll('[contenedor]');
        for (let contenedor of contenedores) {
            this.addEventoEliminarContenedor(contenedor);
        }
    }

    addEventoEliminarContenedor(div) {
        let basura = div.querySelector('#eliminar');
        basura.addEventListener('click', ev => {
            let dlg = new Modal();
            dlg.innerHtml = 'Desea eliminar este item?';
            dlg.open().then(data => {
                div.remove();
            });
        });
    }

    getShadowRoot() {
        let copia = this.shadowRoot.querySelector('[div-custom]').cloneNode(true);
        let componentes = copia.querySelectorAll('[componente]');
        for(let c of componentes) {            
            let div = document.createElement('div');
            let padre = c.parentNode;
            let padreShadown = this.shadowRoot.querySelector(`#${padre.id}`);
            let c2 = padreShadown.querySelector('[componente]');
            div.innerHTML= c2.getShadowRoot();
            div.setAttribute('crear-componente', c.tagName);
            div.setAttribute('tipo', c.getAttribute('tipo'));
            c.after(div);
            c.remove();
        }        
        
        let shadow = `
            ${this.codigoInicial}
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            <div class="w3-row" div-x>
            <div class="w3-col s12">
                <div class="${copia.classList.value}" div-custom propiedades style="min-height: 50px">
                ${copia.innerHTML}
                </div>
            </div>
            </div>            
        `;
        return shadow;
    }

    addListeners() {
        let div = this.shadowRoot.querySelector('[div-custom]');
        div.addEventListener('drop', ev => this.drop(ev, div));
    }

    drop(ev, col) {
        if (this.item1 && this.item2 && this.item1?.id != this.item2?.id) {
            swapElementsDom(this.item1, this.item2);
            this.item1 = null;
            this.item2 = null;
        }
        if (this.item1 && this.item1?.id  == this.item2?.id) {
            col.append(this.item1);            
        }        
        const jsonapp = ev.dataTransfer.getData("app");
        let app = null;
        if (jsonapp) {
            app = JSON.parse(jsonapp);
            this.agregaTagApp(app.tag, col);
        }
        ev.preventDefault();
        ev.stopPropagation();
    }

    agregaTagApp(tag, col) {
        const div = createContenido(tag, tag, this.propiedades);
        this.addListenersItem(div);
        col.append(div);
    }

    addListenersItem(div){
        div.addEventListener('dragover', ev => this.allowDrop(ev, div));
        div.addEventListener('dragstart', ev => this.dragStarSwap(ev, div));
    }

    allowDrop(ev, div) {
        this.item2 = div;
        ev.preventDefault();
    }

    dragStarSwap(ev, div){
        this.item1 = div;
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('[div-x]');        
        const div = creaDiv(original);
        return this.getInnerHtml(div);
    }

    getStyle() {
        let estilos = '';
        let componentes = this.shadowRoot.querySelectorAll('[componente]');
        let conjunto = new Set();
        for(let c of componentes) {
            const tag = c.tagName;
            if (!conjunto.has(tag)) {
                conjunto.add(tag);
                estilos += c.getStyle();
            }
        }
        return style + estilos;        
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('[div-x]')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }

    getWebComponenteMetodos() {
        let codigo = '';
        const div = this.shadowRoot.querySelector('[div-custom]');        
        codigo += getMetodosWebcomponente(div, 'html');
        return codigo;
    }

    getWebComponenteInit(){        
        const div = this.shadowRoot.querySelector('[div-custom]');        
        let codigo = getMetodosWebcomponente(div, 'html');        
        return codigo;
    }
}

customElements.define("x-div", DivCustom);