import { config } from '../../config.js';
import { getDB } from '../../utils/db/db.js';
import { loadCSSExtra } from '../../utils/html/ventana.js';
import { addWindowConParametros } from '../../utils/ventana/agregarApps.js';
import { dataCss, dataJs } from '../vistas/codigos-iniciales.js';
const template = /*html*/`
<div class="w3-bar w3-large w3-blue-grey">
  <a href="#" class="w3-bar-item w3-button"><i class="fa fa-bars"></i></a>
   <span class="w3-bar-item"><img class="bar-img" src="./img/icon/logo-app.png"> W3 Estudio - ide</span>  
</div>
<div style="height: 100vh; padding: 5%;">
    <div class="w3-row">
        <h4>Iniciar página nueva</h4>
        <x-menu-windows menu></x-menu-windows>
    </div>
    <br>
    <div class="w3-row">
        <h4>Iniciar de template</h4>
        <x-menu-aero menu-templates></x-menu-aero>
    </div>
</div>
`;

const style= /*css*/`
.bar-img{
    width: 24px;
}
`;

const menus = [
    {
        titulo: 'W3css',
        css: 'w3css',
        imagen: './img/icon/logo-app.png',
        icono: 'fa-css3',
        color: 'w3-blue',
    },{
        titulo: 'Bootstrap',
        css: 'bootstrap',
        icono: 'fa-bold',
        color: 'w3-indigo',
    },{
        titulo: 'Sketchy',
        css: 'sketchy',
        icono: 'fa-bold',
        color: 'w3-teal',
    },{
        titulo: 'Materia',
        css: 'materia',
        icono: 'fa-bold',
        color: 'w3-green',
    },{
        titulo: 'Literia',
        css: 'literia',
        icono: 'fa-bold',
        color: 'w3-pink',
    },{
        titulo: 'Superhero',
        css: 'superhero',
        icono: 'fa-bold',
        color: 'w3-purple',
    },{
        titulo: 'Minty',
        css: 'minty',
        icono: 'fa-bold',
        color: 'w3-red',
    }
];

const menuTemplates = [
    {
        titulo: 'CV',
        css: 'w3css',
        imagen: './img/ide/preview/cv-preview.png',
        icono: 'fa-file-o',
        color: '',
        componente: 'x-cv'
    },
    {
        titulo: 'Servicios',
        css: 'w3css',
        imagen: './img/ide/preview/disenio-preview.png',
        icono: 'fa-file-o',
        color: '',
        componente: 'x-template-servicios'
    }
]

class PaginadorIde extends HTMLElement {
    constructor() {
        super();
        this.render();
        this.addListeners();
        this.db = getDB();
        this.idProyecto = null;
    }

    render() {
        let shadowRoot = this.attachShadow({ mode: 'open'});        
        shadowRoot.innerHTML = `
            <link rel="stylesheet" href="${config.w3css}">
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;        
        this.menu = shadowRoot.querySelector('[menu]');
        this.menu.setMenu(menus);
        this.menuTemplates = shadowRoot.querySelector('[menu-templates]');
        this.menuTemplates.setMenu(menuTemplates);
    };

    addListeners() {
        this.menu.addEventListener('select', ev => {
            this.menuSeleccionado(ev.detail.menu);
        });
        this.menuTemplates.addEventListener('select', ev => {
            this.menuTemplatesSeleccionado(ev.detail.menu);
        });
    }

    menuSeleccionado(menu) {        
        let app = addWindowConParametros('x-ide', `Nuevo ${menu.titulo}`, null, true, {            
            css: menu.css
        });
    }

    menuTemplatesSeleccionado(menu){
        let componente = document.createElement(menu.componente);
        if (componente.setColor){            
            componente.setColor('blue');
        }
        let data = {
            id: 1,
            codigo: dataJs,
            estilo: dataCss,
            componentes: [{
                componente: componente.tagName,
                shadowRoot: componente.getShadowRoot()
            }]
        }
        let app = addWindowConParametros('x-ide', `Nuevo ${menu.titulo}`, null, true, {            
            css: menu.css
        });
        app.setVista(data);
    }

    connectedCallback() {        
        
    }
}

customElements.define('x-paginador-ide', PaginadorIde);

