export function toCammelCase(tag) {
    let names = tag.split('-');
    let nombre = '';
    for (let name of names) {
        nombre += name[0].toUpperCase();        
        if (name.length > 1) {
            nombre += name.substr(-(name.length-1))
        }
    }
    return nombre;
}


export function toTagCase(nombre) {    
    return nombre.trim().replace(' ', '-');
}