import { config } from "../../../../config.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding linea w3-margin-top">
<div class="w3-col s12">
    <hr class="" propiedades>
</div>
</div>

`;
const style = '';
class LineaWcc extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `                        
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.linea')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.linea')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-linea", LineaWcc);