import { tagToClassName } from "./export-web-component.js";

function getListaProps(obj){
    let salida = '';
    for(let p in obj.props) {
        salida += `    @Input() ${p}: any;\n`;
    }
    return salida;
}
function getValorVariable(valor) {
    if (typeof valor == 'string') {
        return `'${valor}'`;
    }    
    if (typeof valor == 'object') {
        return JSON.stringify(valor);
    }
    return valor;
}

function getVariables(customScript) {
    let variables = '';
    const data = customScript.data;
    for (let key in data) {
        variables += `    ${key} = ${getValorVariable(data[key])};\n`;
    }
    return variables;
}

function getFunciones(obj){
    let fun = '';
    const data = obj.metodos;
    for (let f in data) {
        fun += `    ${data[f].toString()}\n`;
    }
    fun += '    ' + obj.init.toString()+'\n';
    return fun;
}

export function getAngularComponent(tag, componentes, customStyle, obj) {  
    const nombreClase = tagToClassName(tag);
    let estilo = new Set();
    let style = '';
    let template = '';
    let metodos = '';
    let init = '';
    for(let item of componentes) {
        if (!estilo.has(item.tagName)){
            estilo.add(item.tagName);
            style += item.getStyle();
        }
        template += item.getAngularTemplate();
        const metodo = item.getAngularMetodos();
        if (metodo){
            metodos += metodo + '\n';
        }
        init += item.getAngularInit();
    }
    template 
    let text = `
import { Component, Input, OnInit, ViewChild, ElementRef } from '@angular/core';

const template = \`
${template}
\`;

const style = \`
.espacio{
    min-height: 10px;
}
${style}
${customStyle}
\`;
@Component({
    selector: '${tag}',
    template: template,
    styles: [style]
})
export class ${nombreClase}Component implements  OnInit{
${getVariables(obj)}    
    ${getListaProps(obj)}
    constructor(){        
    }

    ngOnInit() {
        ${init}
        this.init();
    }
    ${metodos}
    ${getFunciones(obj)}
}
    `;
    text = text.replace(/lang-/g,'(');
    text = text.replace(/-rang/g,')');
    return text;
}