import { ArchivoTexto } from "./utils.js";
/**
 * Regresa el ArchivoTexto package.json
 * contiene las dependecias del proyeto.
 * @param {string} nombre nombre de la aplicación
 */
export function getPakageJson(nombre) {
    return new ArchivoTexto('package.json',`
{
    "name": "${nombre.trim().replace(/ /g,'-')}",
    "version": "1.0.0",
    "description": "",
    "main": "",
    "scripts": {
        "start": "parcel src/index.html",
        "build": "parcel build src/index.html"
    },
    "keywords": [],
    "author": "",
    "license": "ISC",
    "dependencies": {
        "app.vue": "^0.1.10",
        "vue": "^2.6.12",
        "vue-hot-reload-api": "^2.3.4",
        "vue-router": "^3.4.3"
    },
    "devDependencies": {
        "@vue/component-compiler-utils": "^3.2.0",
        "vue-template-compiler": "^2.6.12"
    }
}
    `);
}