import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding progress-bar w3-margin-top">
<div class="w3-col s12">
    <div class="w3-light-grey" propiedades>
        <div class="w3-container w3-green" style="width:50%" propiedades contenteditable="true">50%</div>
    </div>
</div>
</div>

`;
const style = '';
class ProgressBar extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `
            <style>${style}</style>
            ${template}
        `;                
    }

    getHtml() {
        const div = this.shadowRoot.querySelector('.progress-bar')    
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.progress-bar')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-progress-bar", ProgressBar);