import { addNotificacion, NOTIFICACION } from '../../utils/notificacion/notificaciones.js';
import { PaginadorVistas } from '../vistas/paginador-vistas.js';

let con = Date.now();

class PaginadorUsuarios extends PaginadorVistas {
    constructor() {
        super();
        this.tabla = 'usuarios';
        this.titulo = 'Usuarios';
        this.color = 'indigo';
        this.vistaPrevia = 'x-usuario-vista-previa';
        this.icono = 'user-circle';
    }
    //sobreescribimos
    addItem() {
        let item = {
            nombre: 'Usuario',
            apellidos: 'apellidos',
            username: 'User'+con++,
            password: '123456',
            idProyecto: this.idProyecto,
            grupo: ''
        };
        this.db.post(this.tabla, item).then(data => {
            this.lista.push(data);
            this.paginador.setLista(this.lista);            
        });
    }
    /**
     * 
     * @param {HTMLElement} vistaPrevia 
     * @param {Object} item 
     */
    onVistaPrevia(vistaPrevia, item) {
        vistaPrevia.setIdProyecto(this.idProyecto);
        vistaPrevia.setItem(item);
        vistaPrevia.addEventListener('actualizar', ev => {
            const re = ev.detail;
            this.actualizaItem(re.item)
        });
    }

    actualizaItem(item) {
        this.db.put(this.tabla, item).then(data => {
            this.cargaLista()
            addNotificacion(NOTIFICACION.SUCCESS,
                '<i class="fa fa-check-square-o"></i> Actualizado', 
                'Ha actualizado correctamente al usuario',
                5000);
        });
    }

    onOpenItem(item) {

    }
}

customElements.define('x-paginador-usuarios', PaginadorUsuarios);