export const styleClonador = /*css */`
.clonador:hover {
    border: 1px solid #ccc!important;    
}
.clonador {
    position: relative;
}
.clonador #btn-clonador {
    color: rgb(1 1 1 / 0.2);
}
.clonador #btn-clonador .fa-clone:hover {
    color: black;
}
.clonador #btn-clonador .fa-trash:hover {
    color: red;
}
`;