import {BaseComponentIde} from './componente-ide.js';
import {config} from '../../../config.js';
import { styleEditable } from '../../../comun-style-editable.js';
import { styleClonador } from './style-clonar.js';

export class BaseW3CSS extends BaseComponentIde{
    constructor() {
        super();
    }
    /** Agrega el template básico de los componentes w3css */
    render() {
        super.render();
        this.shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <style>${styleEditable}${styleClonador}</style>
        <style id="custom-style"></style>
        `;        
    }
}