import { appsBS } from "./apps/apps-bootstrap.js";
import { 
    appsFootersW3css, 
    appsHeadersW3css, 
    appsVistasW3css, 
    appsW3css } 
    from "./apps/apps-w3css.js";



export function getApps(css) {    
    if(css !== 'w3css' && css !== 'null') {
        return {
            apps: appsBS,
            appsHeaders: appsBS,
            appsVistas: appsBS,
            appsFooters: appsBS
        }
    }
    return {
        apps: appsW3css,
        appsHeaders: appsHeadersW3css,
        appsVistas: appsVistasW3css,
        appsFooters: appsFootersW3css
    }
}