
import {template} from './template.js';
import { getHtml } from '../../vanilla/exports/export-html.js';
import { getApps} from './apps.js';
import {createContenido, swapElements} from './utils.js';
import { downloadSource, downloadHtml, downloadFile } from '../../vanilla/utils/json/util-json.js';
import {openWindow} from '../../vanilla/utils/html/ventana.js';
import { getWebcomponent } from '../../vanilla/exports/export-web-component.js';
import { ModalGet } from '../../vanilla/utils/modal/modal-get.js';
import { getVueComponent } from '../../vanilla/exports/export-vue.js';
import { getAngularComponent } from '../../vanilla/exports/export-angular.js';
import { addWindow } from '../../vanilla/utils/ventana/agregarApps.js';
import { Openfile } from '../../vanilla/utils/modal/open-file.js';
let contPrueba = 1;

let open = new Openfile();

export function cretateVueApp(div){
let app = new Vue({
    el: div,
    data(){
        return {
            apps: [],
            buscar: [],
            hover: null,
            propiedades: null,
            customStyle: '',
            customScript: '{}',
            editorCss: null,
            editorJs: null,
            css: '',
            estilo: {
                maxHeight: '100px',
                overflow: 'auto'
            },
            estiloColComponentes: {
                maxHeight: '100px',
                overflow: 'auto'
            },
            estiloUi: {
                height: "100vh",
                backgroundImage: "url(./img/cuadro.png)",
                backgroundRepeat: "round",
                overflow: "auto",
                maxHeight: '100px'
            }
        }
    },
    mounted(){    
        this.createDragDrop(); 
        const eliminar = this.$el.querySelector('#eliminar');
        eliminar.addEventListener('click', (e) => {                    
            e.target.parentNode.parentNode.remove();
        });    
        let propiedades = this.$el.querySelector('#propiedades');
        propiedades.innerHTML = '<x-propiedades></x-propiedades>';        
        this.propiedades = propiedades.querySelector('x-propiedades');
    },
    beforeDestroy() {
        open.finaliza();
    },
    template: template,
    methods: {
        setTipo(tipo, css) {            
            this.css = css;
            const appcss = getApps(css);
            switch (tipo) {
                case 'vistas':{
                    this.apps = appcss.appsVistas;
                    break;
                }
                case 'headers':{
                    this.apps = appcss.appsHeaders;
                    break;
                }
                case 'footers':{
                    this.apps = appcss.appsFooters;
                    break;
                }
            
                default:
                    this.apps = appcss.apps;
                    break;
            }
        },
        saveComponentes() {            
            let json = this.getComponentes();            
            let componente = {
                componentes: json,
                codigo: this.editorJs.getCodigo(),
                estilo: this.editorCss.getCodigo()
            }            
            downloadFile(JSON.stringify(componente, null, '\t'), 'componentes.json', 'application/json');
        },
        setPadre(padre) {            
            // Crea una instancia de observer
            let that = this;
            let observer = new MutationObserver(function(mutations) {
                let altura = padre.offsetHeight-10;
                that.estilo.maxHeight = (altura) + 'px';
                that.estiloColComponentes.maxHeight = (altura-50) + 'px';
                that.estiloUi.maxHeight = (altura-50) + 'px';
            });
            // Configura el observer:
            var config = { attributes: true, childList: true, characterData: true };            
            // pasa al observer el nodo y la configuracion
            observer.observe(padre, config);
        },
        getComponentes() {
            let json = [];
            const componentes = this.$el.querySelectorAll('[componente]');
            for(let item of componentes) {
                json.push({
                    componente: item.tagName,
                    shadowRoot: item.getShadowRoot()
                });                
            }
            return json;
        },
        openComponente() {                                   
            open.open().then(r => {
                let json = JSON.parse(r);
                this.setComponentes(json.componentes);
                this.editorCss.setCodigo(json.estilo);
                this.editorJs.setCodigo(json.codigo);
            });            
        },
        setComponentes(componentes) {
            const content = this.$el.querySelector('#content-page'); 
            let panel = content.querySelector('#panel');
            if (panel && componentes.length > 0) {
                panel.remove();
            }
            for (let x of componentes){
                let div = createContenido(x.componente, x.componente, this.propiedades, x.shadowRoot, this.css);                
                this.addListeners(div);
                this.dropNuevo(content, div);
            }
        },
        setEditores(editorJs, editorCss) {
            this.editorCss = editorCss;
            this.editorJs = editorJs;
            this.propiedades.setEditores(editorJs, editorCss);
        },
        setCustomtyle(style){
            this.customStyle = style;
        },
        setCustomScript(script){
            this.customScript = script;            
        },
        vistaPrevia(){            
            const componentes = this.$el.querySelectorAll('[componente]');
            let html = getHtml(componentes, this.customStyle, {});
            openWindow(html);
        },
        testWebComponente() {
            let test = addWindow('test-custom-w3-webcomponent', 'Prueba componente', null, true);
            let ventana = test.parentNode;
            let obj = {};
            eval(`obj = ${this.customScript}`);
            const componentes = this.$el.querySelectorAll('[componente]');
            const tag = `web-componente${contPrueba++}`;
            let webComponente = getWebcomponent(tag, componentes, this.customStyle, obj, this.css);
            test.setCustomComponent(tag, webComponente);
            setTimeout(()=> {
                ventana.setAttribute('activo', '1');
            }, 100);

        },
        exportHtml(){
            let obj = {};
            eval(`obj = ${this.customScript}`);            
            const componentes = this.$el.querySelectorAll('[componente]');
            let html = getHtml(componentes, this.customStyle, obj)
            downloadHtml(html, 'index.html', 'text/html');            
        },
        exportWebComponente() {
            let obj = {};
            eval(`obj = ${this.customScript}`);
            let dlg = new ModalGet('Dame el tag del web componente', 'x-ejemplo');
            dlg.open().then(re => {
                const componentes = this.$el.querySelectorAll('[componente]');
                let webComponente = getWebcomponent(re.data, componentes, this.customStyle, obj, this.css);
                downloadSource(webComponente, `${re.data}.js`, 'text/javascript');
            });
        },
        exportVueComponente(){
            let obj = {};
            eval(`obj = ${this.customScript}`);
            let dlg = new ModalGet('Dame el tag del web componente', 'x-ejemplo');
            dlg.open().then(re => {
                const componentes = this.$el.querySelectorAll('[componente]');
                let webComponente = getVueComponent(componentes, this.customStyle, obj);
                downloadHtml(webComponente, `${re.data}.vue`, 'text/javascript');
            });
        },
        exportAngularComponente() {
            let obj = {};
            eval(`obj = ${this.customScript}`);
            let dlg = new ModalGet('Dame el tag del web componente', 'x-ejemplo');
            dlg.open().then(re => {
                const componentes = this.$el.querySelectorAll('[componente]');
                let webComponente = getAngularComponent(re.data, componentes, this.customStyle, obj);
                
                downloadSource(webComponente, `${re.data}.component.ts`, 'text/javascript');
            });
        },
        buscaPadreContenedor(div) {
            let contenedor = div;
            while (contenedor.getAttribute('contenedor') !=  'true') {
                contenedor = contenedor.parentNode;
            }
            return contenedor;
        },
        show(id) {                    
            const acoordeon = this.$el.querySelector('#'+id);
            if (acoordeon.classList.contains('w3-hide')) {
                acoordeon.classList.remove('w3-hide');
            } else {
                acoordeon.classList.add('w3-hide');
            }
        },
        createDragDrop(){
            let i = 0;
            for (let grupo of this.apps){
                const id = `#grupo${i}`;
                const drag = document.querySelector(id);                        
                i++;
                this.createDrag(drag);
            }
        },
        createDrag(drag){
            let dragGhost = null;                     
        },
        dragStarSwap(ev){            
            const padre = this.buscaPadreContenedor(ev.target)
            ev.dataTransfer.setData("swap", padre.id);
            ev.dataTransfer.setData("tipo-mover-ide", 'swap');
            ev.stopPropagation();
        },
        dragStar(ev, app){
            ev.dataTransfer.setData("app", JSON.stringify(app));
            ev.dataTransfer.setData("tipo-mover-ide", 'nuevo');
            ev.stopPropagation();
        },
        drop(ev){            
            const jsonapp = ev.dataTransfer.getData("app");
            const id = ev.dataTransfer.getData("swap");
            const tipo = ev.dataTransfer.getData("tipo-mover-ide");
            if (!tipo) { // proviene de mover la ventana
                return;
            }
            let app = null;
            if (jsonapp) {
                app = JSON.parse(jsonapp); 
            }
            const content = this.$el.querySelector('#content-page');
            let div = null;
            if (app) {
                div = createContenido(app.nombre, app.tag, this.propiedades, null, this.css);                
                this.addListeners(div);
                let item = div.querySelector(app.tag);
                item.setCustomStyle(this.customStyle);
            }
            if (!this.hover){                        
                this.dropNuevo(content, div);
            } else {
                if (id) {
                    this.dropSwap(id, content);
                }else {
                    this.dropHover(content, div);
                }
            }
            this.hover = null;
            ev.preventDefault();
            ev.stopPropagation();
        },
        agregaAlFinal(app){            
            const content = this.$el.querySelector('#content-page');
            let div = createContenido(app.nombre, app.tag, this.propiedades, null, this.css);                
            this.addListeners(div);
            this.dropNuevo(content, div);
        },
        addListeners(div){
            div.addEventListener('dragover', this.allowDropItem.bind(this));
            div.addEventListener('dragstart', this.dragStarSwap.bind(this));
        },
        dropSwap(id,content){
            let div = content.querySelector('#'+id);            
            if (div && div.id != this.hover.id){    
                const padrediv = this.buscaPadreContenedor(div);
                const padrehover = this.buscaPadreContenedor(this.hover);
                swapElements(padrediv, padrehover);
            }
        },
        dropHover(content, div){            
            content.insertBefore(div, this.buscaPadreContenedor(this.hover));
        },
        dropNuevo(content, div){                                        
            content.appendChild(div);
        },
        allowDrop(ev){
            ev.preventDefault();
            ev.stopPropagation();
        },
        allowDropItem(ev) {
            this.hover = ev.target;                    
            ev.preventDefault();
            ev.stopPropagation();
        },
        swapPaneles() {
            const panelComponentes = this.$el.querySelector('#componentes');
            const panelPropiedades = this.$el.querySelector('#propiedades');
            swapElements(panelComponentes, panelPropiedades);
        }

    }
});
return app;
}