/**
 * Crea el codigo del template por columna
 * @param {HTMLElement} columna 
 */
export function getComponentes(columna, tipo) {
    let componentes = columna.querySelectorAll('[componente]');    
    let codigo = '';
    for (let c of componentes) {        
        if (tipo == 'html') {
            codigo += c.getHtml();
        } else if (tipo == 'vue') {
            codigo += c.getHtml();
        } else if (tipo == 'angular') {
            codigo += c.getHtml();
        }
    }
    return codigo;
}

