import { getDB } from '../../../../utils/db/db.js';
import { JSArchivo } from '../../vue/utils.js';
import { toCammelCase } from "../../../../exports/nombres.js";

function getRoutingApp() {
    return new JSArchivo('src/app/app-routing.module.ts', `
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }        
`);
}

function getAppComponent() {
    return new JSArchivo('src/app/app.component.ts', `
import { Component } from '@angular/core';

const template = \`
<router-outlet></router-outlet>
\`;

@Component({
    selector: 'app-root',
    template: template,
})
export class AppComponent {
    title = '';
}
    
    `);
}



async function getAppModule(idProyecto) {
    let db = getDB();
    let grupos = await db.list('grupos');
    grupos = grupos.filter(grupo => {return grupo.idProyecto == idProyecto});
    let imports = '';
    let declaraciones = '';
    for(let grupo of grupos) {
        const nombre = grupo.nombre.trim()
            .toLowerCase()
            .replace(/ /g,'-');
        const nombreClase = toCammelCase(nombre);
        imports += `import { ${nombreClase}Module } from 'src/flujos/${nombre}/${nombre}.module';`;
        declaraciones += `      ${nombreClase}Module,\n`;
    }
    return new JSArchivo('src/app/app.module.ts',
    `
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
${imports}


@NgModule({
    declarations: [
        AppComponent
    ],
    imports: [
        BrowserModule,
        AppRoutingModule,
${declaraciones}
    ],
    providers: [],
    bootstrap: [AppComponent]
})
export class AppModule { }
    `);
}

export async function getAppFiles(idProyecto) {
    let modulo = await getAppModule(idProyecto);
    return [
        getRoutingApp(),
        getAppComponent(),
        modulo
    ];
}