import { ArchivoTexto } from '../../vue/utils.js';

const tsconfigApp = ` 
{
    "extends": "./tsconfig.json",
    "compilerOptions": {
      "outDir": "./out-tsc/app",
      "types": []
    },
    "files": [
      "src/main.ts",
      "src/polyfills.ts"
    ],
    "include": [
      "src/**/*.d.ts"
    ]
}
`

function getTsconfigApp() {
    return new ArchivoTexto('tsconfig.app.json', tsconfigApp)
}

const tsconfig = `/* To learn more about this file see: https://angular.io/config/tsconfig. */
{
  "compileOnSave": false,
  "compilerOptions": {
    "baseUrl": "./",
    "outDir": "./dist/out-tsc",
    "sourceMap": true,
    "declaration": false,
    "downlevelIteration": true,
    "experimentalDecorators": true,
    "moduleResolution": "node",
    "importHelpers": true,
    "target": "es2015",
    "module": "es2020",
    "lib": [
      "es2018",
      "dom"
    ]
  }
}
`;

function getTsconfig() {
    return new ArchivoTexto('tsconfig.json', tsconfig)
}

const tsconfigSpec = `/* To learn more about this file see: https://angular.io/config/tsconfig. */
{
  "extends": "./tsconfig.json",
  "compilerOptions": {
    "outDir": "./out-tsc/spec",
    "types": [
      "jasmine"
    ]
  },
  "files": [
    "src/test.ts",
    "src/polyfills.ts"
  ],
  "include": [
    "src/**/*.spec.ts",
    "src/**/*.d.ts"
  ]
}
`;

function getTsConfigSpec() {
    return new ArchivoTexto('tsconfig.spec.json', tsconfigSpec);
}
/**
 * Crea los archivos de configuracion de typescript
 * @returns {[ArchivoTexto]} regresa un arreglo de archivos de texto
 */
export function getTsConfigFiles() {
    return [ getTsconfig(), getTsConfigSpec(), getTsconfigApp() ];
}