import { getVueComponent } from '../../../exports/export-vue.js';
import { getDB } from '../../../utils/db/db.js';
import { downloadFile, formatoCodigoHtml } from '../../../utils/json/util-json.js';
import { getPakageJson } from './packagejson.js';
import { getMain, getApp, generarRutas} from './templates.js';
import { ArchivoTexto, getComponentes } from './utils.js';
import { getW3Index } from './w3css-index.js';
let db = getDB();
/**
 * Genera el arrglo de ArchivoTexto con las vistas de
 * la aplicación.
 * Todas las vistas se guardaran en src/vistas
 */
async function getVistasVue(tabla, idProyecto) {       
    let vistas = await db.list(tabla);    
    vistas = vistas.filter(vista => { return vista.idProyecto == idProyecto});    
    let vistasVue = [];
    for(let i=0; i<vistas.length; i++) {
        let obj = {};        
        let vista = await db.get('metadataVistas', vistas[i].id);        
        eval(`obj = ${vista.codigo}`);
        let tag = vistas[i].nombre
            .toLowerCase()
            .replace(/ /g, '-');
        let componentes = getComponentes(vista);
        vistasVue.push(
            new ArchivoTexto(`src/${tabla}/${tag}.vue`,
                formatoCodigoHtml(
                    getVueComponent(componentes, vista.estilo, obj) 
                ))
        );        
    }
    return vistasVue;
}
/**
 * Descarga el proyecto vue como zip. 
 * @param {number} idProyecto id del proyecto
 * @param {string} tipo tipo del css
 */
export async function getProyectoVue(idProyecto, tipo) {
    let archivos = [];
    const proyecto = await db.get('proyectos', idProyecto);
    let vistas = await getVistasVue('vistas', idProyecto);
    let headers = await getVistasVue('headers', idProyecto);
    let footers = await getVistasVue('footers', idProyecto);    
    archivos = archivos.concat(vistas);
    archivos = archivos.concat(headers);
    archivos = archivos.concat(footers);
    archivos = archivos.concat([getPakageJson(proyecto.nombre)]);
    archivos = archivos.concat([getW3Index(proyecto.nombre, tipo)]);
    archivos = archivos.concat([getMain()]);
    let rutas = await generarRutas(idProyecto);    
    archivos = archivos.concat(rutas);
    archivos = archivos.concat([getApp()]);
    let zip = new JSZip();
    for(let file of archivos) {
        zip.file(file.nombre, file.contenido);
    }    
    zip.generateAsync({type:"blob"}).then(function(content) {        
        downloadFile(content, proyecto.nombre + '.vue.zip', 'application/zip');
    });
}

