export const style = /*css*/`
.espacio {
    min-height: 30px;
}
.propiedad-nombre{
    background: #182c3e;
    color: #99a9b8;
}
.propiedad-titulo{
    background: #182c3e;
    color: #99a9b8;
}
.propiedad-titulo:hover{    
    color: white;
}
.propiedad-content{    
    border-left: 6px solid #182c3e;
    border-right: 6px solid #182c3e;
    border-bottom: 6px solid #182c3e;
    overflow: auto;
    max-height: 100px;
}
.propiedad-border{    
    border: 1px solid #182c3e;    
}
.propiedad-editar{
    outline: none;    
}
.propiedad-editar:focus{
    border-bottom: solid 1px;
    background: white !important;
    color: tomato !important;
}
.propiedad-fila{
    border-bottom: solid 1px black;
}
.propiedad-fila:hover, .propiedad-fila:active{
    background: white !important;
}
.basura{
    color: grey;
}
.basura:hover{
    color: tomato;
}
.ide-grupo{
    background: #182c3e;
    color: #99a9b8;
    width: -webkit-fill-available;
}
.ide-grupo:hover {
    background-color: #163048 !important;
    color: white !important;
}
.ide-item{    
    transition: transform 0.1s;
    font-size: 0.8em;
    width: auto;
}
[contenedor]{    
    transition: transform 0.1s;    
    width: -webkit-fill-available;
}
[contenedor]:hover, .ide-item:hover {
    transform: scale(0.99);
}
.btn-color{
	float: left;
    width: 24px;
    height: 24px;
    margin: 2px;
}
`;