import { formatoCodigoHtml, formatoCodigoJs } from "../../../utils/json/util-json.js";

/**
 * Clase para definir la estructura de los archivos de texto.
 * Utilizada para exportar.
 */
export class ArchivoTexto {
    /**     
     * @param {string} nombre Nombre del archivo. Puede definir que este dentro de alguna carpeta. ejemplo src/hola.txt
     * @param {string} contenido Contenido del archivo en forma de texto.
     */
    constructor(nombre, contenido) {
        this.nombre = nombre;
        this.contenido = contenido;
    }
}

export class JSArchivo extends ArchivoTexto {
    /**     
     * @param {string} nombre Nombre del archivo. Puede definir que este dentro de alguna carpeta. ejemplo src/hola.txt
     * @param {string} contenido Contenido del archivo en forma de texto.
     */
    constructor(nombre, contenido) {
        super(nombre, contenido);        
        this.contenido = formatoCodigoJs(contenido);
    }
}

export class VueArchivo extends ArchivoTexto {
    /**     
     * @param {string} nombre Nombre del archivo. Puede definir que este dentro de alguna carpeta. ejemplo src/hola.txt
     * @param {string} contenido Contenido del archivo en forma de texto.
     */
    constructor(nombre, contenido) {
        super(nombre, contenido);        
        this.contenido = formatoCodigoHtml(contenido);
    }
}

/**
 * Regresa una lista de componentes web.
 * Esto porque los metodos de export los tiene 
 * los webcomponentes.
 * @param {[HTMLElement]} vista Vista con los componentes. Es una fila de la tabla metadataVistas.
 */
export function getComponentes(vista) {    
    let lista = [];
    for (let x of vista.componentes) {
        let tagItem = document.createElement(x.componente);
        tagItem.setShadowRoot(x.shadowRoot);
        tagItem.connectedCallback();
        lista.push(tagItem);
    }    
    return lista;
}

/**
 * Regresa una lista de rutas
 * @param {string} ruta Ruta a la que se le sumara el nuevo contexto
 * @param {nodo} nodo Nodo del flujo de rutas.
 */
export function creaArbol(ruta, nodo) {    
    if (!nodo) {return [];}
    const sruta = ruta == '/' ? '' : ruta;
    let rutaInicial = sruta + '/' + nodo.contexto;
    let lista = [{
        ruta: rutaInicial,
        nodo: nodo
    }];      
    for (let hijo of nodo.caminos) {
        lista = lista.concat(creaArbol(rutaInicial, hijo));
    }
    return lista;
}

export function getNombreArchivo(nombre){
    return nombre.trim().toLowerCase().replace(/ /g, '-');
}

/**
 * Busca el camino correspondiente al id del grupo
 * @param {number} id id de grupo
 * @param {[any]} caminos arreglo de caminos
 */
export function getCamino(id, caminos) {
    let filter = caminos.find(item => item.id == id);
    return filter;
};