import { config } from "../../config.js";

/**
 * Abre una nueva ventana con el codigo dado. 
 * Esto sirve para la ayuda o para vista previa.
*/
export function openWindow(src) {
    let vistaPrevia = window.open("", "myWindow", "width='+w',height='+h',status=yes,toolbar=no,menubar=no,location=no");
    let html = vistaPrevia.document.querySelector('html');
    html.innerHTML = src;    
    return vistaPrevia;  
}

export function loadCSSExtra(css, tema = 'default') {
    let style = document.querySelector('#estilo-boostrap');
    if (!style) {
        style = document.createElement('style');
        style.setAttribute('rel', 'stylesheet');
        style.id = 'estilo-bootstrap';
        let head = document.querySelector('head');
        head.appendChild(style);
    }    
    if (css == 'bootstrap') {
        style.setAttribute('href',config.bootstrap.default);
    }
}