import { getAngularComponent } from '../../../exports/export-angular.js';
import { getDB } from '../../../utils/db/db.js';
import { downloadFile, formatoCodigoHtml } from '../../../utils/json/util-json.js';
import { JSArchivo, getComponentes } from '../vue/utils.js';
import { getModulo } from './archivos/templateModulo.js';

import {toCammelCase} from '../../../exports/nombres.js';
import { getPakageJson } from './archivos/packagejson.js';
import { getAngularJson } from './archivos/angularjson.js';
import { getTsConfigFiles } from './archivos/tsconfig.js';
import { getPruebas } from './archivos/pruebas.js';
import { getConfiguraciones } from './archivos/configuracion.js';
import { getMainFiles, getW3Index } from './archivos/mainFiles.js';
import { generarRutas } from './archivos/templateFlujos.js';
import { getAppFiles } from './archivos/app-angular.js';

let db = getDB();
/**
 * Genera el arrglo de ArchivoTexto con las vistas de
 * la aplicación.
 * Todas las vistas se guardaran en src/${tabla}/
 * @returns {[JSArchivo]} regresa un arreglo con la lista de archivos
 */
async function getVistasAngular(tabla, idProyecto) {       
    let vistas = await db.list(tabla);    
    vistas = vistas.filter(vista => { return vista.idProyecto == idProyecto});    
    let vistasAngular = [];
    let imports = '';
    let declaraciones = '';
    let exports = '';
    for(let i=0; i<vistas.length; i++) {
        let obj = {};        
        let vista = await db.get('metadataVistas', vistas[i].id);        
        eval(`obj = ${vista.codigo}`);
        let tag = vistas[i].nombre
            .toLowerCase()
            .replace(/ /g, '-');
        let componentes = getComponentes(vista);
        const nombreClase = toCammelCase(tag)+'Component';
        imports += `import { ${nombreClase} } from './${tag}.component';\n`;
        declaraciones +=`       ${nombreClase},\n`;
        exports += `        ${nombreClase},\n`;
        vistasAngular.push(
            new JSArchivo(`src/${tabla}/${tag}.component.ts`,
                formatoCodigoHtml(
                    getAngularComponent(tag, componentes, vista.estilo, obj) 
                ))
        );        
    }
    let modulo = getModulo(tabla, imports, declaraciones, exports);
    vistasAngular.push( new JSArchivo(`src/${tabla}/${tabla}.module.ts`, modulo) );
    return vistasAngular;
}

/**
 * Descarga el proyecto vue como zip.
 */
export async function getProyectoAngular(idProyecto, tipo) {
    let archivos = [];
    const proyecto = await db.get('proyectos', idProyecto);
    let vistas = await getVistasAngular('vistas', idProyecto);
    let headers = await getVistasAngular('headers', idProyecto);
    let footers = await getVistasAngular('footers', idProyecto);
    archivos = archivos.concat(vistas);
    archivos = archivos.concat(headers);
    archivos = archivos.concat(footers);

    archivos = archivos.concat([getPakageJson(proyecto.nombre)]);
    archivos = archivos.concat([getAngularJson()]);
    archivos = archivos.concat(getTsConfigFiles());
    archivos = archivos.concat(getPruebas());
    archivos = archivos.concat(getConfiguraciones());
    
    
    archivos = archivos.concat([getW3Index(proyecto.nombre, tipo)]);
    archivos = archivos.concat(getMainFiles());

    let rutas = await generarRutas(idProyecto);
    archivos = archivos.concat(rutas);
    
    let appFiles = await getAppFiles(idProyecto);
    archivos = archivos.concat(appFiles);
    
    let zip = new JSZip();
    for(let file of archivos) {
        zip.file(file.nombre, file.contenido);
    }    
    zip.generateAsync({type:"blob"}).then(function(content) {        
        downloadFile(content, proyecto.nombre + '.angular.zip', 'application/zip');
    });
}