import { config } from "../../../../config.js";
import { BaseW3CSS } from "../../base/componente-w3css.js";
const template = /*html*/`
<div class="w3-row-padding titulo-h2-icono">
<div class="w3-col s12" contenedor>    
    <h2 propiedades> <i class="fa fa-gear w3-text-blue"></i> <span contenteditable="true">Título</span></h2>    
</div>
</div>

`;
const style = '';
class TituloH2Icono extends BaseW3CSS {
    constructor() {
        super();
        this.render();
    }

    render() {
        super.render();
        let shadowRoot = this.shadowRoot;
        shadowRoot.innerHTML += `
            <link rel="stylesheet" href="${config.fontAwesome}">
            <style>${style}</style>
            ${template}
        `;        
    }

    getHtml() {
        const original = this.shadowRoot.querySelector('.titulo-h2-icono')    
        const div = original.cloneNode(true);  
        return this.getInnerHtml(div);
    }

    getStyle() {
        return style;
    }

    getScrip() {
        return '';
    }

    getAngularTemplate() {
        const original = this.shadowRoot.querySelector('.titulo-h2-icono')    
        const div = original.cloneNode(true);
        return this.getInnerHTMLAngular(div);
    }
}

customElements.define("x-titulo-h2-icono", TituloH2Icono);