/**
 * Intercambia dos elementos que estan en pantalla de posición.
 * NO funciona si los elementos no estan montados en el doom.
 * @param {HTMLElement} obj1 
 * @param {HTMLElement} obj2 
 */
export function swapElementsDom(obj1, obj2) {
    var temp = document.createElement("div");
    obj1.parentNode.insertBefore(temp, obj1);
    obj2.parentNode.insertBefore(obj1, obj2);
    temp.parentNode.insertBefore(obj2, temp);
    temp.parentNode.removeChild(temp);
}