import  { config } from "../../../config.js";
import { Grupo} from "../../editor-caminos/itemsCanvas.js";
import { addWindowConParametros } from "../../../utils/ventana/agregarApps.js";
import { getDB } from '../../../utils/db/db.js';
import { addNotificacion, NOTIFICACION } from "../../../utils/notificacion/notificaciones.js";
import { ModalForm } from "../../../utils/modal/modal-form.js";
import { validaProyecto } from "../../proyecto/utils-proyecto.js";

const template = /*html*/`
<x-bar-menu color="w3-blue"></x-bar-menu>
<x-paginador-files titulo="Grupos" vista-previa="x-vista-previa-flujo"></x-paginador-files>
`;

const style = /*css*/`
`;
//TODO definir en donde se editara el nombre del grupo
//TODO definir las condiciones
//TODO definir lo objetos globales
class PaginadorGrupos extends HTMLElement {
    constructor() {
        super();        
        this.db = getDB();
        this.lista = [];
        this.activo = null;  
        this.idProyecto = null;           
    }

    connectedCallback() {
        this.idProyecto = parseInt(this.getAttribute('idproyecto'));        
        validaProyecto(this);
        this.render();
        this.addListeners();
        this.loadGrupos();
    }

    render() {
        let shadowRoot = this.attachShadow({mode: 'open'});
        shadowRoot.innerHTML = `
        <link rel="stylesheet" href="${config.w3css}">
        <link rel="stylesheet" href="${config.fontAwesome}">
        <style>${style}</style>
        ${template}
        `;
        this.paginador = this.shadowRoot.querySelector('x-paginador-files');
        this.paginador.setLista(this.lista);
        this.padre = this.parentNode;
        this.menu = shadowRoot.querySelector('x-bar-menu');
        this.menu.setItem({
            titulo: 'Grupos',            
            icono: 'users',
            acciones: [{
                titulo: 'Nuevo',
                icono: 'fa-file-o',
            }]
        });
    }

    loadGrupos() {
        this.db.list('grupos').then(data =>{            
            this.lista = data.filter(item => {return item.idProyecto == this.idProyecto});            
            this.paginador.setLista(this.lista);
        });
    }

    addListeners() {
        this.addListenerEditarFlujo();
        this.addListenerVistaPrevia();
        this.addListenerGuardar();
        this.addListenerEditarNombre();
    }

    addListenerEditarNombre() {
        this.padre.addEventListener('editar', ev => {            
            if (!this.activo) {return;}
            let dlg = new ModalForm();
            dlg.innerHtml = `
            <div class="w3-container">
                <label>Nombre de grupo</label>
                <input class="w3-input" type="text" name="nombre" dato-salida value="${this.activo.nombre}">
            </div>
            `;
            dlg.titulo = 'Cambiar nombre de grupo';
            dlg.open().then(data => {
                this.activo.nombre = data.data.nombre;
                this.paginador.setLista(this.lista);
                addNotificacion(NOTIFICACION.SUCCESS, 
                    '<i class="fa fa-check-square-o"></i> Editado',
                    'Se cambio el nombre');
            });
        });
    }

    addListenerEditarFlujo() {
        this.paginador.addEventListener('onOpenItem', (e) => {
            const r = e.detail;            
            let app = addWindowConParametros('x-editor-flujos', r.item.nombre, null, true, 
                {idProyecto: this.idProyecto});
            this.db.get('caminos', r.item.id).then(data => {                
                app.setListaObjectos(data.flujo);
                app.actualiza();
                app.draw();
            });
            app.addEventListener('guardar', ev => {
                let flujo = ev.detail.flujo;
                let data = {
                    id: r.item.id,
                    flujo: flujo
                }
                this.db.put('caminos', data).then(d => {
                    addNotificacion(NOTIFICACION.SUCCESS, '<i class="fa fa-check-square-o"></i> Guardado',
                    'Se guardo el flujo');                 
                });
            });
        });
    }

    addListenerVistaPrevia() {
        this.paginador.addEventListener('onVistaPrevia', (e) => {
            const r = e.detail;
            this.activo = r.item;
            this.db.get('caminos', r.item.id).then(data => {
                r.vistaPrevia.setLista(data.flujo);
            });
        });
    }

    addListenerGuardar() {
        this.menu.addEventListener('accion', ev => {
            let data = ev.detail;
            if (data.accion.titulo == 'Nuevo') {
                this.abreDlgGuardar(); 
            }
        });
    }

    abreDlgGuardar() {
        let dlg = new ModalForm();
            dlg.innerHtml = `
            <div class="w3-container">
                <label>Nombre de grupo</label>
                <input class="w3-input" type="text" name="nombre" dato-salida>
            </div>
            `;
            dlg.titulo = 'Agregar grupo';
        dlg.open().then(re => {
            const nombre = re.data.nombre;            
            if (this.existeNombre(nombre)) {
                addNotificacion(NOTIFICACION.ALERT, 'Error', 'Ya existe el grupo: '+ nombre);
            }else {
                this.guardaGrupo(nombre);
            }
        });
    }

    existeNombre(nombre) {
        for (let grupo of this.lista) {
            if (grupo.nombre == nombre) {
                return true;
            }
        }
        return false;
    }

    guardaGrupo(nombre) {        
        let grupo = this.crearGrupo(nombre);
        this.db.post('grupos', grupo).then(data => {
            this.lista.push(data);
            let flujo = this.creaFlujo(data.id, data.nombre);
            this.db.post('caminos', flujo, false).then(re => {
                addNotificacion(NOTIFICACION.SUCCESS, '<i class="fa fa-check-square-o"></i> Guardado',
                'Se creo el grupo');
                this.paginador.setLista(this.lista);
            });
        });
    }

    crearGrupo(nombre) {
        return {
            nombre: nombre,
            descripcion: '',
            idProyecto: this.idProyecto
        };
    }

    creaFlujo(id, nombre) {
        let grupo = new Grupo(nombre, 50, 50);
        return {
            id: id,
            flujo: [grupo.toJson()]
        };
    }
}

customElements.define('x-paginador-grupos', PaginadorGrupos);