import { toCammelCase } from "../../../../exports/nombres.js";
export function getModulo(nombre, imports, declaracion, exports) {
    return `
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
${imports}
@NgModule({
    declarations: [
${declaracion}
    ],
    exports: [
${exports}
    ],
    imports: [
    CommonModule,
    RouterModule
    ]
})
export class ${toCammelCase(nombre)}Module { }
`;
}